import React from 'react';
import PropTypes from 'prop-types';
import Loader from '@webui-one/components/Loading/Loader';
import { stringifyObject } from '@webui-one/core/utils/object';
import { color11, color1 } from '@webui-one/styles/ApplicationDefaultStyles';
import { timeStampToISOLocale } from '@webui-one/core/utils/time';
import style from './ActionOutputStyles';
import CrossSymbol from '@webui-one/styles/static/CrossSymbol';

const isResultOk = (resultObj) => {
    if (!resultObj) return false;
    if (!Array.isArray(resultObj)) return false;
    if (!(resultObj.length === 2)) return false;

    const result = resultObj[0];
    if (!Object.prototype.toString.call(result) === '[object Object]') return false;
    if (typeof result.name === 'undefined') return false;
    if (typeof result.value === 'undefined') return false;
    if (result.name === 'result' && result.value === 'true') return true;
    return false;
};

const createResult = (result) => {
    if (!Array.isArray(result)) {
        return (
            <ul style={style.list.body} data-handle="action-output">
                <li style={style.list.row.even}>
                    <p style={style.list.value}>{ JSON.stringify(result) }</p>
                </li>
            </ul>);
    }

    const rows = result.map((r, ix) => (
        <li
            style={ix % 2 === 0 ? style.list.row.even : style.list.row.odd}
            key={`action-output-result-${ix}`}
            >
            <p style={style.list.p}>{ r.name }</p>
            <p style={style.list.value}>{ r.value }</p>
        </li>
    ));
    return (
        <ul style={style.list.body} data-handle="action-output">
            { rows }
        </ul>
    );
};

const ActionOutput = ({
    error,
    result,
    resultPending,
    completedAt,
    hideSuccessfullOutput,
    onClearActionOutput,
}) => {
    let contents = false;
    if (error) {
        contents = (
            <ul style={style.list.body} data-handle="action-output">
                <li style={style.list.row.even} data-handle="action-error">
                    <p style={style.list.value}>Error</p>
                </li>
                {['type', 'code', 'message'].reduce((acc, errorKey) => {
                    if (errorKey in error) {
                        const comp = (<li style={style.list.row.even} data-handle={`action-error-${errorKey}`} key={errorKey}>
                            <p style={style.list.value}>{errorKey}:</p>
                            <p style={style.list.value}>{error[errorKey]}</p>
                        </li>);
                        acc.push(comp);
                    }
                    return acc;
                }, [])}
                {'data' in error && <li style={style.list.row.even} data-handle="action-error-data">
                    <p style={style.list.value}>{stringifyObject(error.data)}</p>
                </li>}
            </ul>);
    }

    // If there was no error in the JsonRpc call itself and
    // The result is OK and
    // You want to hide the successfull output.
    if (!contents && isResultOk(result) && hideSuccessfullOutput) {
        return false;
    }
    if (result) {
        contents = createResult(result);
    }
    if (resultPending) {
        contents = <div style={style.loader}><Loader color={color11} /></div>;
    }
    return (<div style={{ position: 'relative' }}>
        { !resultPending && (result || error) &&
            <button
                data-handle={'clearActionOutput'}
                style={style.clearActionOutputButton}
                onClick={() => { onClearActionOutput(); }}
            >
                <CrossSymbol color={color1}/>
            </button>
        }
        {contents}
        {!resultPending && completedAt &&
            <p style={style.p.info} data-handle="completed-at">
                Completed at {timeStampToISOLocale(completedAt)}
            </p>
        }
    </div>);
};

ActionOutput.propTypes = {
    completedAt: PropTypes.number,
    error: PropTypes.shape({
        data: PropTypes.shape({}),
    }),
    result: PropTypes.oneOfType([
        PropTypes.bool,
        PropTypes.string,
        PropTypes.shape({}),
        PropTypes.arrayOf(
            PropTypes.shape({}),
        ),
    ]),
    resultPending: PropTypes.bool,
    hideSuccessfullOutput: PropTypes.bool,
    onClearActionOutput: PropTypes.func,
};

ActionOutput.defaultProps = {
    hideSuccessfullOutput: false,
};

export default ActionOutput;

export {
    isResultOk as _isResultOk,
};
