import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import ComboBox from '@webui-one/components/ConfigRender/ComboBox/ComboBox';
import ModalConfirm from '@webui-one/components/Modal/ModalConfirm';
import {
    leaf as style,
    error as errorStyle,
} from '@webui-one/components/Modal/ModalConfirmStyles';
import {
    quoteKey,
} from '@webui-one/core/functional_utils';
import {
    pathWithoutKeys,
} from '@webui-one/core/keypath/kp-parser';
import * as S from '@webui-one/core/schema';
import { isDeepEqual } from '@webui-one/core/utils/object';
import { getLeafWidget } from 'Common/Components/ConfigRender/SchemaContainer';
import GetSchemaContainer from 'Common/Containers/GetSchemaContainer';
import * as nodesActions from 'modules/nodes/actions';
import {
    selectChildrenForInput,
} from 'modules/schemas/reducer';
import { selectNode } from 'modules/nodes/selectors';
import { getLinkWithPath } from 'ConfigurationEditor';

export const KEY_PLACEHOLDER = '___KEY_PLACEHOLDER___';

const formIsValid = (values, children) => {
    if (!children) return false;
    if (Array.isArray(children) && children.length === 0) return true;
    if (!values) return false;
    let returnValue = true;
    const keys = Object.keys(values);
    if (keys.length === 0) return false;
    keys.forEach((key) => { if (values[key] === undefined || values[key] === '') returnValue = false; });
    return returnValue;
};

const getChildrenAsObject = (children, state) => (
    children.reduce((acc, cur) => {
        const name = S.name(cur);
        // eslint-disable-next-line no-param-reassign
        acc[name] = { value: state[name], kind: S.kind(cur) };
        return acc;
    }, {})
);

const getLeafListChildrenAsObject = (children, state) => (
    children.reduce((acc, cur) => {
        acc[0] = { value: state[S.name(cur)], kind: S.kind(cur) };
        return acc;
    }, {})
);

const childrenForInputWithKeypath = (path, children) => (
    children.reduce((acc, cur) => {
        const childKeypath = cur.path.replace(pathWithoutKeys(path), path);
        acc.push({ ...cur, path: childKeypath });
        return acc;
    }, [])
);

const getPartialKey = (childrenForInput, newValues) => {
    const parts = childrenForInput
        .filter(c => S.isKey(c))
        .map(c => quoteKey(newValues[S.name(c)] || KEY_PLACEHOLDER));

    const lastIndex = parts.reduceRight((acc, p, i) => {
        if (acc !== undefined) return acc;
        if (p !== KEY_PLACEHOLDER) return i;
        return undefined;
    }, undefined);

    if (lastIndex === undefined) return parts.join(' ');
    return parts.slice(0, lastIndex + 1).join(' ');
};

const getOptions = (nodes, index) => {
    if (!nodes) return [];

    const nodeValues = nodes[index].values;

    return (nodeValues || []).map(value => ({ value }));
};


class ModalSchemaInputContainer extends React.Component {
    constructor(props) {
        super(props);
        this.onModalConfirm = this.onModalConfirm.bind(this);
        this.onSetLeafRefValue = this.onSetLeafRefValue.bind(this);
        this.onSetValue = this.onSetValue.bind(this);
        this.getLeaf = this.getLeaf.bind(this);
        this.state = {
            values: undefined,
        };
        this.hasLoaded = false;
    }

    static getDerivedStateFromProps(props, state) {
        const { childrenForInput } = props;
        const { values: currentValues } = state;
        if (currentValues) return null;

        if (!childrenForInput || childrenForInput.length < 1) return null;

        const values = childrenForInput.reduce((acc, c) => (
            { ...acc, [S.name(c)]: '' }
        ), {});

        return { values };
    }

    componentDidMount() {
        const { childrenForInput, onGetLeafrefValues } = this.props;

        if (childrenForInput && childrenForInput.length > 0) {
            childrenForInput.forEach((child) => {
                if (S.isLeafRef(child)) {
                    onGetLeafrefValues(child.path, child.path);
                }
            });
        }
    }

    shouldComponentUpdate(nextProps, nextState) {
        const {
            childrenForInput,
            nodes,
            path,
            schema,
        } = this.props;
        const { values } = this.state;

        if (path !== nextProps.path) return true;
        if (!(isDeepEqual(childrenForInput, nextProps.childrenForInput))) return true;
        if (!(isDeepEqual(nodes, nextProps.nodes))) return true;
        if (!(isDeepEqual(schema, nextProps.schema))) return true;
        if (!(isDeepEqual(values, nextState.values))) return true;
        return false;
    }

    componentDidUpdate(prevProps) {
        const { childrenForInput, onGetLeafrefValues } = this.props;
        if (!(isDeepEqual(prevProps.childrenForInput, childrenForInput))) {
            childrenForInput.forEach((child) => {
                if (S.isLeafRef(child)) {
                    onGetLeafrefValues(child.path, child.path);
                }
            });
        }
    }

    onModalConfirm() {
        const { childrenForInput, path, schema, onConfirm } = this.props;
        const { values } = this.state;
        if (S.isLeafList(schema)) {
            const items = getLeafListChildrenAsObject(childrenForInput, values);
            return onConfirm(path, items, schema);
        }
        const items = getChildrenAsObject(childrenForInput, values);
        return onConfirm(path, items, schema);
    }

    onSetValue(name, value) {
        const newState = {
            ...this.state,
            values: {
                ...this.state.values,
                [name]: value,
            },
        };
        this.setState(newState);
        return newState;
    }

    onSetLeafRefValue(name, value, isLeafList) {
        const { onGetLeafrefValues, childrenForInput, path } = this.props;
        const { values: newValues } = this.onSetValue(name, value);

        if (!isLeafList) {
            const currentKey = getPartialKey(childrenForInput, newValues);
            childrenForInput.forEach((child) => {
                if (S.isLeafRef(child)) {
                    const leafRefPath = `${path}{${currentKey}}/${S.name(child)}`;
                    onGetLeafrefValues(child.path, leafRefPath);
                }
            });
        }
    }

    getChildren(schemas) {
        return (schemas && schemas.map((schema, index) =>
            this.getLeaf(schema.path, schema, index)));
    }

    getLeaf(keypath, schema, index) {
        const { nodes } = this.props;
        const { values } = this.state;

        const name = S.name(schema);
        const autoFocus = (index === 0);
        let leafWidget;

        const options = getOptions(nodes, index);

        if (S.isLeafRef(schema)) {
            leafWidget = (<ComboBox
                autoFocus={autoFocus}
                formatDepsTarget={getLinkWithPath}
                options={options}
                key={keypath}
                LinkComponent={Link}
                name={name}
                path={keypath}
                value={values[name] || ''}
                editable
                onChange={value => this.onSetLeafRefValue(name, value, S.isLeafList(schema))}
                onSetValue={value => this.onSetLeafRefValue(name, value, S.isLeafList(schema))}
            />);
        } else {
            leafWidget = React.cloneElement(getLeafWidget(keypath, schema, true), {
                autoFocus,
                value: (values[name] || ''),
                onChange: value => this.onSetValue(name, value),
                onSetValue: value => this.onSetValue(name, value),
            });
        }
        return (
            <div
                key={name}
                data-handle={name}
                style={style.container}>
                <div style={style.title}>
                    {name}
                </div>
                { leafWidget }
            </div>
        );
    }

    render() {
        const {
            title,
            childrenForInput,
            closeOnConfirm,
            onClose,
            path,
            schema,
            error,
        } = this.props;
        const { values } = this.state;
        let children;
        if (S.isLeafList(schema)) {
            children = this.getLeaf(path, schema, 0);
        } else {
            children = this.getChildren(childrenForInput);
        }

        return (
            <ModalConfirm
                title={title}
                shouldRender
                onRequestClose={onClose}
                onConfirm={this.onModalConfirm}
                closeOnConfirm={closeOnConfirm}
                formIsValid={formIsValid(values, children)}
                data-handle="modal-title"
                preventAutofocus
                >
                { children }
                { error && <div data-handle="error" style={errorStyle}>{error}</div>}
            </ModalConfirm>
        );
    }
}

ModalSchemaInputContainer.propTypes = {
    childrenForInput: PropTypes.arrayOf(PropTypes.shape(Object)),
    closeOnConfirm: PropTypes.bool,
    error: PropTypes.string,
    nodes: PropTypes.arrayOf(PropTypes.shape(Object)),
    onClose: PropTypes.func,
    onConfirm: PropTypes.func,
    onGetLeafrefValues: PropTypes.func,
    path: PropTypes.string.isRequired,
    schema: PropTypes.shape(Object),
    title: PropTypes.string,
};

ModalSchemaInputContainer.defaultProps = {
};

const mapStateToProps = (state, ownProps) => {
    const { path, schema } = ownProps;

    let nodes;
    let childrenForInput;
    if (S.isLeafList(schema)) {
        childrenForInput = [{ ...schema, path }];
        nodes = [selectNode(state, path)];
    } else {
        childrenForInput = selectChildrenForInput(state, schema, c => c.kind === 'key');
        childrenForInput = childrenForInputWithKeypath(path, childrenForInput);
        nodes = childrenForInput.map(child => selectNode(state, child.path));
    }

    return {
        childrenForInput,
        nodes,
        schema,
    };
};

const mapDispatchToProps = dispatch => ({
    onGetLeafrefValues(listPath, createPath) {
        dispatch(nodesActions.getLeafrefValuesForCreate(listPath, createPath));
    },
});

const ConnectedModalSchemaInputContainer = connect(
    mapStateToProps,
    mapDispatchToProps,
)(ModalSchemaInputContainer);

// eslint-disable-next-line react/prop-types
const WrappedModalSchemaInputContainer = ({ path, kindpath, error, ...rest }) => (
    <GetSchemaContainer path={path} kindpath={kindpath} key={error}>
        <ConnectedModalSchemaInputContainer path={path} error={error} {...rest} />
    </GetSchemaContainer>
);

export default WrappedModalSchemaInputContainer;

export {
    formIsValid as _formIsValid,
    getChildrenAsObject as _getChildrenAsObject,
    getPartialKey as _getPartialKey,
    childrenForInputWithKeypath as _childrenForInputWithKeypath,
    mapStateToProps as _mapStateToProps,
    mapDispatchToProps as _mapDispatchToProps,
    ModalSchemaInputContainer as _ModalSchemaInputContainer,
};
