const create = (initialState, states) => {
    const fsm = { ...states };

    let currentState = initialState;
    let done = false;

    const process = (result) => {
        fsm.previousState = currentState;
        currentState = result.next || currentState;
        done = result.done === true;

        if (done && Object.prototype.hasOwnProperty.call(result, 'value')) {
            return {
                value: result.value,
                done,
            };
        }
        fsm.previousResult = { value: result.value };

        return { value: result.value, done };
    };

    const iterator = {
        next(value) {
            if (!Object.prototype.hasOwnProperty.call(fsm, currentState)) {
                return {
                    value: undefined,
                    done: true,
                };
            }

            const result = fsm[currentState](value, fsm);

            return process(result);
        },
    };

    return iterator;
};

export default create;
