import * as nodesActions from 'modules/nodes/actions';

import {
    getPlanComponentPath,
    getPlanComponentStatePath,
    getComponentKeys,
} from 'modules/plans/selectors';
import { getSchema as _getSchema } from 'modules/schemas2/actions';

const SELECTION_COMPONENT = ['name', 'type'];
const SELECTION_STATE = ['name', 'status'];

/* eslint-disable import/prefer-default-export */
export const getPlanThunk = ({
    path,
    funcs: {
        query = nodesActions.query,
        getSchema = _getSchema,
    } = {} }) => async (dispatch, getState) => {
    await dispatch(getSchema({
        path,
        reload: false,
        single: true,
    }));

    const planComponentPath = getPlanComponentPath(path);
    const componentKeys = getComponentKeys(getState(), planComponentPath);

    const action = await dispatch(query({
        selection: SELECTION_COMPONENT,
        path: planComponentPath,
    }));

    return Promise.all(action.result.results.map((r) => {
        const keys = componentKeys.map(ck => r[ck]);
        return dispatch(query({
            selection: SELECTION_STATE,
            path: getPlanComponentStatePath(path, keys),
        }));
    }));
};
