import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import {
    subscribeChangesAction,
    unsubscribeChangesAction,
} from 'modules/comet/actions';
import {
    selectPlan,
    selectPlanLoading,
} from 'modules/plans/selectors';
import {
    getPlanThunk,
} from 'modules/plans/actions';
import Plan from '@webui-one/components/ConfigRender/Plan/Plan';

class PlanContainer extends React.Component {
    constructor(props) {
        super(props);
        this.refreshPlan = this.refreshPlan.bind(this);
    }

    componentDidMount() {
        const { onLoad, onSubscribe, path } = this.props;
        onSubscribe(path, this.refreshPlan);
        onLoad(path);
    }

    componentWillUnmount() {
        const { onUnsubscribe, path } = this.props;
        onUnsubscribe(path, this.refreshPlan);
    }

    refreshPlan() {
        const { onLoad, path } = this.props;
        onLoad(path);
    }

    render() {
        const { backgroundColor, plan, path, isLoading } = this.props;
        const { statusStats, status, components } = plan;
        return (<Plan
            backgroundColor={backgroundColor}
            statusStats={statusStats}
            path={path}
            isLoading={isLoading}
            status={status}
            components={components}
            LinkComponent={Link}
        />);
    }
}

PlanContainer.propTypes = {
    backgroundColor: PropTypes.string,
    isLoading: PropTypes.bool,
    onLoad: PropTypes.func.isRequired,
    onSubscribe: PropTypes.func.isRequired,
    onUnsubscribe: PropTypes.func.isRequired,
    path: PropTypes.string.isRequired,
    plan: PropTypes.shape(Object),
};


const mapStateToProps = (state, ownProps) => ({
    isLoading: selectPlanLoading(state, ownProps.path),
    plan: selectPlan(state, ownProps.path),
});

const mapDispatchToProps = dispatch => ({
    onLoad(path) {
        dispatch(getPlanThunk({ path }));
    },
    onSubscribe(path, refreshPlan) {
        dispatch(
            subscribeChangesAction([path], refreshPlan),
        );
    },
    onUnsubscribe(path, refreshPlan) {
        dispatch(unsubscribeChangesAction([path], refreshPlan));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(PlanContainer);

export {
    PlanContainer as _PlanContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
