import { connect } from 'react-redux';
import {
    selectLocationPathnameFromPath,
} from 'modules/routing/selectors';
import { selectIsEditMode } from 'ConfigurationEditor/reducer';
import {
    selectInfoArray,
    selectName,
} from 'modules/schemas/reducer';

import ActionLink from './ActionLink';

const mapStateToProps = (state, ownProps) => {
    const isEditMode = selectIsEditMode(state);
    const infoArray = isEditMode ? selectInfoArray(state, ownProps.path) : [];
    return {
        info: infoArray,
        isEditMode,
        label: selectName(state, ownProps.path),
        to: selectLocationPathnameFromPath(state, ownProps.path),
    };
};

export default connect(
    mapStateToProps,
)(ActionLink);
