import React from 'react';
import PropTypes from 'prop-types';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';

const Arcs = ({
    backgroundColor,
    circleRadius,
    verticalSpacing,
    width,
    components,
}) => {
    // progress parameters
    const progress = components.reduce((acc, comp) => {
        acc.total += comp.statusStats.all;
        acc.green += comp.statusStats.reached;
        acc.orange += comp.statusStats['not-reached'];
        return acc;
    }, { total: 0, green: 0, orange: 0 });
    const perimeter = Math.PI * 2 * circleRadius;
    const greenProgress = progress.green / progress.total;
    const greenDash = greenProgress === 1 ? 0 : greenProgress * perimeter;
    const greenDasharray = `${greenDash} 1000`;
    const orangeDash = (progress.orange / progress.total) * perimeter;
    const orangeDasharray = `0 ${greenDash} ${orangeDash} 1000`;

    const strokewidth = 5;
    const height = Math.max(verticalSpacing * (components.length - 1), circleRadius * 2);
    const spacing = components.length > 1 ? verticalSpacing : 0;

    return (<svg
        version="1.2"
        baseProfile="tiny"
        x="0px"
        y="0px"
        width={width}
        height={height}
        viewBox={`0 0 ${width} ${height}`}
        xmlSpace="preserve"
        pointerEvents="none" >
        <g fill="none" strokeWidth="3">
            { components.length && (components.length < 2 ?
                <line
                    x1={circleRadius}
                    y1={height * 0.5}
                    x2={width}
                    y2={height * 0.5}
                    stroke={components[0].statusStats.all > components[0].statusStats.reached ?
                        defaultStyles.color15 : defaultStyles.color14}
                /> :
                components.map((arc, idx) => {
                    const x1 = circleRadius;
                    const y1 = height * 0.5;
                    const rx = width - circleRadius;
                    const ry = (height * 0.5) - (spacing * idx);
                    const inverse = ry > 0;
                    const color = arc.statusStats.all > arc.statusStats.reached ?
                            defaultStyles.color15 : defaultStyles.color14;
                    const data = `M ${x1} ${y1} A ${rx}, ${Math.abs(ry)}, 0, 0,
                    ${inverse ? 1 : 0}, ${width}, ${spacing * idx}`;
                    return <path key={`arc${idx}`} d={data} stroke={color} />;
            }))}
        </g>
        <circle
            cx={circleRadius}
            cy={height * 0.5}
            stroke={backgroundColor}
            strokeWidth={3}
            r={circleRadius + 1.5}
            fill={progress.total > progress.green ? defaultStyles.color15 : defaultStyles.color14}
        />
        <circle
            cx={circleRadius}
            cy={height * 0.5}
            r={circleRadius - (strokewidth * 0.5)}
            fill="none"
            stroke={defaultStyles.color14}
            strokeOpacity="0.5"
            strokeDasharray={greenDasharray}
            transform={`rotate(${-90} ${circleRadius} ${height * 0.5})`}
            strokeWidth={strokewidth}
        />
        <circle
            cx={circleRadius}
            cy={height * 0.5}
            r={circleRadius - (strokewidth * 0.5)}
            fill="none"
            stroke={defaultStyles.color3}
            strokeOpacity="0.5"
            strokeDasharray={orangeDasharray}
            transform={`rotate(${-90} ${circleRadius} ${height * 0.5})`}
            strokeWidth={strokewidth}
        />
        <text
            x={circleRadius}
            y={(height * 0.5) + 8}
            textAnchor="middle"
            fontFamily={defaultStyles.font2}
            fill={defaultStyles.textColor4}
            fontSize={defaultStyles.large1}
            >
            {`${progress.green}/${progress.total}`}
        </text>


    </svg>);
};

Arcs.propTypes = {
    backgroundColor: PropTypes.string,
    circleRadius: PropTypes.number,
    components: PropTypes.arrayOf(PropTypes.object),
    verticalSpacing: PropTypes.number,
    width: PropTypes.number,
};

Arcs.defaultProps = {
    circleRadius: 45,
    verticalSpacing: 96,
    width: 150,
};

export default Arcs;
