import React from 'react';
import PropTypes from 'prop-types';
import getStyle from './CheckBoxStyles';

class Checkbox extends React.Component {
    constructor(props) {
        super(props);
        this.handleKeyPress = this.handleKeyPress.bind(this);
        this.toggleCheckbox = this.toggleCheckbox.bind(this);
        this.buttonStyle = this.buttonStyle.bind(this);
    }

    handleKeyPress(e) {
        if (e.keyCode === 13 || e.keyCode === 32) {
            this.toggleCheckbox();
        }
    }

    toggleCheckbox() {
        this.props.onSetValue(!this.props.value);
    }

    buttonStyle() {
        const { editable, value, styleName } = this.props;
        const style = getStyle(styleName);

        if (!editable) {
            return value ? style.checkbox.noeditChecked : style.checkbox.noedit;
        }

        return value ? style.checkbox.checked : style.checkbox.unchecked;
    }


    render() {
        const { editable, value, styleName, name, children } = this.props;
        const style = getStyle(styleName);
        return (
            <a
                data-label="checkboxLabel"
                role="button"
                onClick={editable ? this.toggleCheckbox : null}
                onKeyDown={editable ? this.handleKeyPress : null}
                tabIndex="0"
                style={style.label}
                >
                <div
                    data-handle={name}
                    style={this.buttonStyle()}
                    role="checkbox"
                    aria-checked={value}
                >
                    {value && <div data-handle="checkboxArrow" style={style.arrow} />}
                </div>
                {children}
            </a>
        );
    }
}

Checkbox.propTypes = {
    children: PropTypes.shape(Object),
    editable: PropTypes.bool,
    name: PropTypes.string,
    onSetValue: PropTypes.func,
    styleName: PropTypes.string,
    value: PropTypes.bool,
};

Checkbox.defaultProps = {
    editable: true,
};

export default Checkbox;
