import React from 'react';
import PropTypes from 'prop-types';
import Link from '../../Link';
import Loader from '../../Loading/Loader';

import style, { getStateStyle, getComponentStyle, setStatus } from './PlanStyles';
import Arcs from './Arcs';

export const States = ({ states, LinkComponent }) =>
    (<div data-handle="states" style={style.statesWrapper}>
        {states.reduce((acc, s, idx) => {
            acc.push(<div
                key={`line-${s.name}`}
                style={setStatus(idx ? style.line : style.firstLine, s.status)}
            />);
            acc.push(
                <LinkComponent
                    key={`state-${s.name}`}
                    data-handle={`state-${s.name}-${s.status}`}
                    style={style.stateWrapper}
                    to={s.to}
                >
                    <div style={getStateStyle(s.status)} />
                    <div style={style.label}>{s.name}</div>
                </LinkComponent>);
            return acc;
            }, [])
        }
    </div>);

States.propTypes = {
    states: PropTypes.arrayOf(
        PropTypes.shape({
            name: PropTypes.string,
            status: PropTypes.string,
        }),
    ),
    LinkComponent: PropTypes.elementType,
};

States.defaultProps = {
    LinkComponent: Link,
};

export const PlanComponent = ({
    name,
    states,
    status,
    statusStats,
    to,
    LinkComponent,
}) => (
    <div data-handle={`component-${name}-${status}`} style={style.componentWrapper}>
        <LinkComponent key={'component-graph'} style={style.componentStatusWrapper} to={to}>
            <div style={getComponentStyle(status)}>
                { statusStats.reached }/{statusStats.all}
            </div>
            <div style={style.firstLabel}>{name}</div>
        </LinkComponent>
        <States
            LinkComponent={LinkComponent}
            states={states}
         />
    </div>
);

PlanComponent.propTypes = {
    name: PropTypes.string,
    states: PropTypes.arrayOf(
        PropTypes.shape({
            name: PropTypes.string,
            status: PropTypes.string,
        }),
    ),
    status: PropTypes.string,
    statusStats: PropTypes.shape(Object),
    to: PropTypes.string,
    LinkComponent: PropTypes.elementType,
};

PlanComponent.defaultProps = {
    LinkComponent: Link,
};

const Plan = ({ components, path, isLoading, LinkComponent, backgroundColor }) => (
    <div style={style.wrapperWrapper}>
        <div data-handle={`kplan-${path}`} style={style.planWrapper}>
            {components.length > 0 ? <div style={style.arcsWrapper}>
                <Arcs components={components} backgroundColor={backgroundColor} />
            </div> :
            <div style={style.emptyMessage}>Plan is empty</div>}
            <div
                data-handle={`components-${path}`}
                style={components.length === 1 ? style.componentsWrapperSingle
                    : style.componentsWrapper}
            >
                { components.map(
                    ({ name, states, status: planStatus, statusStats: planStatusStats, to }) => (
                        <PlanComponent
                            key={name}
                            name={name}
                            states={states}
                            status={planStatus}
                            statusStats={planStatusStats}
                            to={to}
                            LinkComponent={LinkComponent}
                        />
                    ))}
            </div>
            {isLoading &&
                <div style={style.loading}>
                    <Loader color={style.loader.color} />
                </div>
            }
        </div>
    </div>
);

Plan.propTypes = {
    backgroundColor: PropTypes.string,
    components: PropTypes.arrayOf(PropTypes.shape(Object)).isRequired,
    isLoading: PropTypes.bool,
    path: PropTypes.string,
    LinkComponent: PropTypes.elementType,
};

Plan.defaultProps = {
    LinkComponent: Link,
};

export default Plan;
