import React from 'react';
import PropTypes from 'prop-types';

import style from './DateAndTimeStyles';
import { autoComplete, parse, stateSymbols as S } from './utils';
import Suggestions from './Suggestions';

class DateAndTime extends React.Component {
    constructor(props) {
        super(props);
        this.onBlur = this.onBlur.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onFocus = this.onFocus.bind(this);
        this.onKeyDown = this.onKeyDown.bind(this);

        this.state = {
            activeSuggestionIx: 0,
            backspace: false,
            draftValue: props.value,
            error: undefined,
            hasFocus: false,
            suggestions: ['00:00:00', '23:59:59'],
        };
    }

    static getDerivedStateFromProps(props, state) {
        const { value, isLoading } = props;
        const { hasFocus, draftValue } = state;
        if (!hasFocus && !isLoading && value !== draftValue) {
            return { draftValue: value };
        }
        return null;
    }

    shouldComponentUpdate(nextProps, nextState) {
        const { editable, isLoading } = nextProps;
        const { error, value } = nextState;
        if (editable !== this.props.editable) return true;
        if (isLoading !== this.props.isLoading) return true;
        if (error !== this.state.error) return true;
        if (value !== this.state.draftValue) return true;
        return false;
    }

    onBlur() {
        this.setState({ showSuggestions: false, hasFocus: false });
        this.handleSetValue();
    }

    onChange(e) {
        const { backspace } = this.state;
        let value = e.target.value;
        let parsed = parse(value);
        let showSuggestions = false;
        let error;

        if (parsed && parsed.duplicateSeparator) return;

        if (!backspace && parsed && parsed.separator && !value.endsWith(parsed.separator)) {
            value = `${value}${parsed.separator}`;
            parsed = parse(value);
        }

        if (parsed && parsed.state && parsed.state === S.DAY_HOUR_SEP) {
            showSuggestions = true;
        }

        if (parsed && parsed.error) {
            error = parsed.error;
        }

        this.setState({
            error,
            showSuggestions,
            draftValue: value,
        });
    }

    onFocus() {
        this.setState({ hasFocus: true });
    }

    onKeyDown(e) {
        const { value } = this.props;
        const { activeSuggestionIx, draftValue, showSuggestions, suggestions } = this.state;

        e.persist();
        if (e.key === 'Enter' || e.key === 'Tab') {
            if (showSuggestions) {
                this.setState({
                    showSuggestions: false,
                    activeSuggestionIx: 0,
                    draftValue: `${draftValue}${suggestions[activeSuggestionIx]}`,
                });
            } else {
                this.setState({ draftValue: autoComplete(draftValue) });
            }
        }

        if (e.key === 'Escape') {
            this.setState({
                showSuggestions: false,
                activeSuggestionIx: 0,
                draftValue: value,
            }, () => e.target.blur());
        }

        if (e.key === 'Backspace') {
            this.setState({ backspace: true });
        } else {
            this.setState({ backspace: false });
        }

        if (e.key === 'ArrowUp' && showSuggestions) {
            if (activeSuggestionIx === 0) return;
            this.setState({ activeSuggestionIx: activeSuggestionIx - 1 });
        }

        if (e.key === 'ArrowDown' && showSuggestions) {
            if (activeSuggestionIx - 1 >= suggestions.length) return;
            this.setState({ activeSuggestionIx: activeSuggestionIx + 1 });
        }
    }

    getInputStyle() {
        const { editable, isOperdata } = this.props;
        const { error } = this.state;

        if (!editable) {
            return isOperdata ? style.inputFieldReadOnlyOperdata : style.inputFieldReadOnly;
        }
        if (error) return style.error;
        return style.input;
    }

    handleSetValue() {
        const { onSetValue, value } = this.props;
        const { draftValue } = this.state;

        if (draftValue !== value) {
            onSetValue(draftValue);
        }
    }

    render() {
        const {
            autoFocus,
            editable,
            name,
        } = this.props;

        const {
            activeSuggestionIx,
            draftValue,
            showSuggestions,
            suggestions,
        } = this.state;

        const inputStyle = this.getInputStyle();

        return (
            <div style={style.relative}>
                <div
                    style={editable ? style.wrapper : style.wrapperReadOnly}
                    data-handle={`date-and-time-${name}`}
                >
                    <input
                        autoFocus={autoFocus}
                        onBlur={this.onBlur}
                        onChange={this.onChange}
                        onFocus={this.onFocus}
                        onKeyDown={this.onKeyDown}
                        placeholder="yyyy-mm-ddThh:mm:ss"
                        readOnly={!editable}
                        style={inputStyle}
                        type="text"
                        value={draftValue}
                    />
                </div>
                <Suggestions
                    showSuggestions={showSuggestions}
                    draftValue={draftValue}
                    suggestions={suggestions}
                    activeSuggestionIx={activeSuggestionIx}
                />
            </div>
        );
    }
}

DateAndTime.propTypes = {
    autoFocus: PropTypes.bool,
    editable: PropTypes.bool,
    isLoading: PropTypes.bool,
    isOperdata: PropTypes.bool,
    name: PropTypes.string,
    value: PropTypes.string,
    onSetValue: PropTypes.func,
};

DateAndTime.defaultProps = {
    value: '',
};

export default DateAndTime;
