import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
    getLeafrefValues,
    hideAction,
} from 'modules/nodes/actions';

import { isDeepEqual } from '@webui-one/core/utils/object';
import { selectDepNodes, selectNode } from 'modules/nodes/selectors';
import { selectConfigForPath } from 'modules/data/selectors';
import {
    createThunk,
    deleteThunk,
    existsThunk,
    getValueThunk,
    setValueThunk,
} from 'modules/nodes/thunks';
import {
    selectInfoArray,
} from 'modules/schemas/reducer';
import { selectIsEditMode } from 'ConfigurationEditor/reducer';
import WidgetLeaf from './WidgetLeaf';

const canValueBeDeleted = (value, newValue) => (
    value !== undefined || newValue !== undefined
);

const isDefaultValueHelper = (value, newValue, defaultValue) => (
    defaultValue !== undefined
        && value === undefined
        && newValue === undefined
);

// ENG-20233 optization: avoid unnecessary get_leafref_values calls
const shouldRefreshValues = (currentDepsNodes, previousDepsNodes, isLoading) =>
    !isDeepEqual(previousDepsNodes, currentDepsNodes) && !isLoading;

class LeafContainer extends React.Component {
    // eslint-disable-next-line camelcase, react/sort-comp
    UNSAFE_componentWillMount() {
        const { isLeafref } = this.props;

        if (isLeafref) return this.componentWillMountLeafref();

        return undefined;
    }

    componentDidUpdate(prevProps) {
        const { isLeafref, isLoading, leafDepsNodes } = this.props;
        if (isLeafref) {
            if (shouldRefreshValues(leafDepsNodes, prevProps.leafDepsNodes, isLoading)) {
                this.props.onGetLeafrefValues();
            }
        }
    }

    getLeaf() {
        const { children, defaultValue, newValue, value, onSetValue } = this.props;
        const isDefaultValue = isDefaultValueHelper(value, newValue, defaultValue);
        const nodeValue = newValue === undefined ? value : newValue;
        const leafValue = isDefaultValue ? defaultValue : nodeValue;
        return React.cloneElement(
            children,
            {
                value: leafValue,
                onSetValue,
                isDefaultValue,
                defaultValue,
            },
        );
    }

    getEmptyLeaf() {
        const { children, exists, onCreate, onDelete } = this.props;
        return React.cloneElement(children, { exists, onCreate, onDelete });
    }

    getLeafref() {
        const { children, newValue, value, values, onSetValue, deps } = this.props;
        const childProps = {
            value: (newValue && newValue.length > 0) ? newValue : value,
            options: values.map(v => ({ value: v })),
            onSetValue,
            deps,
        };
        return React.cloneElement(children, childProps);
    }

    componentWillMountLeafref() {
        const { onGetLeafrefValues } = this.props;
        onGetLeafrefValues();
    }

    render() {
        const {
            deletable,
            error,
            exists,
            info,
            isDirty,
            isEditMode,
            isEmptyLeaf,
            isHidden,
            isLeafref,
            isLoading,
            isMandatory,
            isOperdata,
            name,
            newValue,
            onDelete,
            onHide,
            value,
        } = this.props;

        let child;
        if (isEmptyLeaf) {
            child = this.getEmptyLeaf();
        } else if (isLeafref) {
            child = this.getLeafref();
        } else {
            child = this.getLeaf();
        }

        const canBeDeleted = !isMandatory && deletable && child.props.editable &&
            (isEmptyLeaf ? exists : canValueBeDeleted(value, newValue));

        return (
            <WidgetLeaf
                error={error}
                canBeDeleted={canBeDeleted}
                info={info}
                isDirty={isDirty}
                isEditMode={isEditMode}
                isHidden={isHidden}
                isLoading={isLoading}
                isMandatory={isMandatory}
                isOperdata={isOperdata}
                isEmptyLeaf={isEmptyLeaf}
                exists={exists}
                name={name}
                onHide={onHide}
                onDelete={onDelete}
                >
                { child }
            </WidgetLeaf>
        );
    }
}

// REVIEW: Props that only is used in mapStateToProps,
// should we defined them in PropTypes?
LeafContainer.propTypes = {
    children: PropTypes.shape({}),
    deletable: PropTypes.bool,
    error: PropTypes.shape({}),
    exists: PropTypes.bool,
    info: PropTypes.arrayOf(PropTypes.object),
    deps: PropTypes.arrayOf(PropTypes.string),
    isEmptyLeaf: PropTypes.bool,
    isDirty: PropTypes.bool,
    isEditMode: PropTypes.bool,
    isHidden: PropTypes.bool,
    isLoading: PropTypes.bool,
    isLeafref: PropTypes.bool,
    isMandatory: PropTypes.bool,
    isOperdata: PropTypes.bool,
    leafDepsNodes: PropTypes.arrayOf(PropTypes.object),
    name: PropTypes.string.isRequired,
    newValue: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.arrayOf(PropTypes.string),
        PropTypes.bool,
    ]),
    value: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.arrayOf(PropTypes.string),
        PropTypes.bool,
    ]),
    defaultValue: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.arrayOf(PropTypes.string),
        PropTypes.bool,
    ]),
    values: PropTypes.arrayOf(PropTypes.string),
    onCreate: PropTypes.func,
    onDelete: PropTypes.func,
    onHide: PropTypes.func.isRequired,
    onGetLeafrefValues: PropTypes.func,
    onSetValue: PropTypes.func,
};

LeafContainer.defaultProps = {
    info: [],
    values: [],
};

const leafPropsFromNode = (config, node = {}) => {
    const { error, isDirty, isHidden, isLoading } = node;
    const { newValue, value } = config;

    return {
        error,
        isDirty,
        isHidden,
        isLoading,
        newValue,
        value,
    };
};

const emptyLeafPropsFromNode = (config, node) => {
    const { exists } = config;

    return {
        ...leafPropsFromNode(config, node),
        exists,
    };
};

const leafrefPropsFromNode = (config, node = {}) => {
    const { reloadValues, values } = node;

    return {
        ...leafPropsFromNode(config, node),
        reloadValues,
        values,
    };
};

const mapStateToProps = (state, ownProps) => {
    const { deps, dependants, isEmptyLeaf, isLeafref, path } = ownProps;
    const config = selectConfigForPath(state, path) || {};
    const node = selectNode(state, path);
    const isEditMode = selectIsEditMode(state);
    const infoArray = isEditMode ? selectInfoArray(state, path) : [];
    if (isEmptyLeaf) {
        return {
            ...emptyLeafPropsFromNode(config, node),
            isEditMode,
            dependants,
            info: infoArray,
        };
    }

    if (isLeafref) {
        const leafDepsNodes = selectDepNodes(state, path, deps);
        return {
            ...leafrefPropsFromNode(config, node),
            isEditMode,
            leafDepsNodes,
            dependants,
            info: infoArray,
        };
    }

    return {
        ...leafPropsFromNode(config, node),
        isEditMode,
        dependants,
        info: infoArray,
    };
};

const mapDispatchToPropsEmptyLeaf = (dispatch, ownProps) => {
    const { caseAncestors, dependants, exists, path } = ownProps;

    const dispatchProps = {
        onCreate() {
            dispatch(createThunk({ caseAncestors, dependants, path }));
        },
        onDelete() {
            dispatch(deleteThunk({ caseAncestors, dependants, path }));
        },
        onExists() {
            dispatch(existsThunk({ caseAncestors, dependants, exists, path }));
        },
        onHide(value) {
            dispatch(hideAction(ownProps.path, value));
        },
        dispatch,
    };
    return dispatchProps;
};

const mapDispatchToPropsLeaf = (dispatch, ownProps) => {
    const {
        caseAncestors,
        dependants,
        path,
        isConfigFalseCallpoint,
    } = ownProps;

    const dispatchProps = {
        onDelete() {
            dispatch(deleteThunk({ caseAncestors, dependants, path }));
        },
        onSetValue(value) {
            return dispatch(setValueThunk({
                caseAncestors,
                dependants,
                path,
                value,
            }));
        },
        onGetValue() {
            dispatch(getValueThunk({ path, isConfigFalseCallpoint }));
        },
        onHide(value) {
            dispatch(hideAction(ownProps.path, value));
        },
        dispatch,
    };
    return dispatchProps;
};

const mapDispatchToPropsLeafref = (dispatch, ownProps) => ({
    ...mapDispatchToPropsLeaf(dispatch, ownProps),
    onGetLeafrefValues() {
        dispatch(getLeafrefValues(ownProps.path));
    },
});

const mapDispatchToProps = (dispatch, ownProps) => {
    if (ownProps.isEmptyLeaf) {
        return mapDispatchToPropsEmptyLeaf(dispatch, ownProps);
    }

    if (ownProps.isLeafref) {
        return mapDispatchToPropsLeafref(dispatch, ownProps);
    }

    return mapDispatchToPropsLeaf(dispatch, ownProps);
};

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(LeafContainer);

export {
    LeafContainer as _LeafContainer,
    isDefaultValueHelper as _isDefaultValueHelper,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
    canValueBeDeleted as _canValueBeDeleted,
};
