import React from 'react';
import PropTypes from 'prop-types';

const NotSupported = ({ kind, type }) => {
    if (kind) {
        return (
            <div>
                <p style={{ fontSize: '0.8em' }}>Not Supported:</p>
                <p style={{ fontSize: '0.6em' }}>{kind}</p>
            </div>
        );
    }
    const typeName = type ? `${type.namespace}:${type.name}` : ':';
    return (
        <div>
            <p style={{ fontSize: '0.8em' }}>Not Supported:</p>
            <p style={{ fontSize: '0.6em' }}>{typeName}</p>
        </div>
    );
};


NotSupported.propTypes = {
    kind: PropTypes.string,
    type: PropTypes.shape({
        namespace: PropTypes.string,
        name: PropTypes.string,
    }),
};

export default NotSupported;
