import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Grid from '@webui-one/components/Grid/Grid';
import { selectIsRootPath as _selectIsRootPath } from 'modules/routing/selectors';
import {
    loadActionAction as _loadActionAction,
    runActionThunk as _runActionThunk,
} from 'modules/yangActions/actions';
import { selectInfo as _selectInfo } from 'modules/schemas/reducer';
import ActionRunButton from './ActionRunButton';
import ActionOutputContainer from './ActionOutputContainer';

class ActionContainer extends React.Component {
    constructor(props) {
        super(props);
        this.onRunAction = this.onRunAction.bind(this);
    }

    componentDidMount() {
        const { path, onLoadAction } = this.props;
        onLoadAction(path);
    }

    onRunAction() {
        const { path, refreshPath, onRunAction } = this.props;
        const shouldRefresh = path === refreshPath;
        onRunAction(path, shouldRefresh);
    }

    render() {
        const {
            enabled,
            label,
            path,
            info,
            hideSuccessfullOutput,
        } = this.props;
        const buttons = [
            <ActionRunButton
                enabled={enabled}
                onClick={this.onRunAction}
                info={info}
                label={label}
                key="action"
            />];

        return (
            <div>
                <Grid>{buttons}</Grid>
                <ActionOutputContainer
                    path={path}
                    hideSuccessfullOutput={hideSuccessfullOutput}
                />
            </div>
        );
    }
}

ActionContainer.propTypes = {
    enabled: PropTypes.bool,
    info: PropTypes.string,
    label: PropTypes.string,
    path: PropTypes.string,
    onLoadAction: PropTypes.func,
    onRunAction: PropTypes.func,
    hideSuccessfullOutput: PropTypes.bool,
    refreshPath: PropTypes.string,
};

ActionContainer.defaultProps = {
    hideSuccessfullOutput: false,
    enabled: true,
    label: 'action',
};

const mapStateToProps = (
    state,
    {
        path,
        funcs: {
            selectInfo = _selectInfo,
            selectIsRootPath = _selectIsRootPath,
        } = {},
    } = {},
) => ({
    info: selectInfo(state, path),
    isRootPath: selectIsRootPath(state, path),
});

const mapDispatchToProps = (
    dispatch,
    {
        funcs: {
            loadActionAction = _loadActionAction,
            runActionThunk = _runActionThunk,
        } = {},
    } = {},
) => ({
    onLoadAction(path) {
        dispatch(loadActionAction({ path }));
    },
    onRunAction(path, shouldRefresh) {
        dispatch(runActionThunk({ path, shouldRefresh }));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ActionContainer);

export {
    ActionContainer as _ActionContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
