import React from 'react';
import PropTypes from 'prop-types';
import AddSymbol from '@webui-one/styles/static/AddSymbol';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import style from './PresenceButtonStyles';

const PresenceButton = ({ onCreate, disabled, path }) => (<button
    style={style.button}
    disabled={disabled}
    onClick={onCreate}
    data-handle={`PresenceButton-${path}`}
    >
    <AddSymbol color={disabled ? defaultStyles.color6 : defaultStyles.color1} />
</button>);

PresenceButton.propTypes = {
    disabled: PropTypes.bool,
    onCreate: PropTypes.func,
    path: PropTypes.string,
};

PresenceButton.defaultProps = {
    disabled: false,
};

export default PresenceButton;
