import React from 'react';
import { escapeKey, formatPath } from '@webui-one/core/utils/path';
import {
    timeStampToDuration,
    timeStampToRelativeTimeString,
} from '@webui-one/core/utils/time';
import Link from '../../Link';
import CheckBox from '../CheckBox/CheckBox';
import Key from './Key';
import RelativeTimeStamp from './RelativeTimeStamp';
import styles from './ListStyles';

/* Headers */
export const createHeaderFactory = (styleName = 'defaultStyle', additionalStyle = {}) => (key, title) => {
    const style = { ...styles(styleName).list.header.title, ...additionalStyle };
    return <div style={style} key={key}>{title}</div>;
};

export const createHeader = createHeaderFactory();

export const createSortableHeaderFactory =
    (sortAction, sortOrder, additionalStyle = {}) => (key, title) => {
        const sortStyle = styles('defaultStyle').list.header.sortArrow;
        let sortIndicator = <span>{' '}</span>;
        if (sortOrder.columnKey === key) {
            sortIndicator = <span style={sortStyle}>{sortOrder.isDesc ? '' : ''}</span>;
        }
        const style = { ...styles('defaultStyle').list.header.titleSortable, ...additionalStyle };
        /* eslint-disable jsx-a11y/no-static-element-interactions */
        return (<div style={style} key={key} onClick={() => sortAction(key)}>
            {title} {sortIndicator}
        </div>);
    /* eslint-enable jsx-a11y/no-static-element-interactions */
    };

export const createEmptyHeaderFactory = styleName => (key, title) =>
    <div style={styles(styleName).list.header.empty} key={key} title={title} />;

export const createEmptyHeader = createEmptyHeaderFactory('defaultStyle');

export const createCheckboxHeaderFactory = (isSelected, onChange, styleName) => name => (
    <div key={name} style={styles(styleName).list.checkboxHeader} data-handle="select-all">
        <CheckBox
            onSetValue={v => onChange(v)}
            styleName="listStyles"
            value={isSelected}
        />
    </div>
);

/* Cells */
export const createCheckBoxCellFactory = (onChange, styleName) => (isSelected, key, rowKey) => (
    <div
        key={key}
        style={styles(styleName).list.checkboxCell}
        data-handle={`checkbox-cell-${rowKey}`}
        >
        <CheckBox
            onSetValue={v => onChange(v, rowKey)}
            styleName="listStyles"
            value={isSelected}
        />
    </div>
);

export const createDirtyCellFactory = styleName => isDirty => (
    <div style={styles(styleName).list.row.dirty}>
        { isDirty &&
            <div
                style={styles('defaultStyle').list.row.dirtyActive}
                data-handle="dirtyActive"
            />
        }
    </div>);

export const createDirtyCell = createDirtyCellFactory('defaultStyle');

export const createKeyCellFactory = (applicationName, LinkComponent = Link) =>
    (name, cellKey, rowKey) => {
        const to = formatPath(`${applicationName}${escapeKey(rowKey)}`);
        return <Key to={to} value={name} LinkComponent={LinkComponent} />;
    };

export const createTextCellFactory = styleName => name =>
    <div style={styles(styleName).list.row.text}>{name}</div>;

export const createTextCell = createTextCellFactory('defaultStyle');

export const createEmptyCell = name =>
    <div style={styles('defaultStyle').list.row.text}>{name}</div>;

export const createDateCellFactory = timer => ts =>
    (<RelativeTimeStamp
        timeStamp={ts}
        timeFormatter={timeStampToRelativeTimeString}
        timer={timer}
    />);

export const createDurationCellFactory = timer => isoDate =>
    (<RelativeTimeStamp
        timeStamp={Date.parse(isoDate)}
        timeFormatter={timeStampToDuration}
        timer={timer}
    />);

/* Additional */
export const getDefaultColumn = () => ({
    key: 'defaultKey',
    title: 'Title',
    canBeHidden: true,
    isExpandable: false,
    isSticky: false,
    isVisible: true,
    createHeader,
    createCell: createTextCell,
});
