import React from 'react';
import PropTypes from 'prop-types';
import { isIE11 } from '@webui-one/core/utils/browser';
import { chunk, range } from '@webui-one/core/utils/array';
import { isDeepEqual } from '@webui-one/core/utils/object';

import {
    style,
    getDivColumnStyle,
    getGridCellStyle,
    getGridColumnGapStyle,
    getGridColumnsStyle,
} from './GridStyles';


const getRowContentWidth = (rows) => {
    let width;
    if (rows && rows.children && rows.children.length > 0) {
        const widths = Array.from(rows.children).map(c => c.getBoundingClientRect().width);
        width = Math.max(...widths);
    }
    return width;
};

const getContainerWidth = (container) => {
    let width;
    if (container) {
        width = container.getBoundingClientRect().width;
    }
    return width;
};


const calculateGrid = (widthOfList, widthOfContainer, divs, minimumRows) => {
    let columnCount;

    if (widthOfContainer && widthOfList) {
        // 16 might seems like a magic number but it should be same amount as column gap (1rem).
        columnCount = Math.floor(widthOfContainer / (widthOfList + 16)) || 1;
    }
    let rowCount = Math.ceil(divs.length / (columnCount || 1));
    rowCount = rowCount < minimumRows ?
        Math.min(minimumRows, divs.length) : rowCount;

    return { columnCount, rowCount };
};

const getGridColumnGap = (gapIndex, rowCount) => (
    <div style={getGridColumnGapStyle(gapIndex, rowCount)} key={`gap-${gapIndex}`} />
);

const getGridGaps = (childrenCount, columnCount, rowCount) => (
    range(0, columnCount).reduce((acc, cur) => {
        if (childrenCount > (rowCount * (cur + 1))) {
            acc.push(getGridColumnGap((cur * 2) + 2, rowCount));
        }
        return acc;
    }, [])
);

const hasStateChanged = (state, nextState) => {
    const { columnCount, rowCount, widthOfList } = state;
    if (columnCount !== nextState.columnCount) return true;
    if (rowCount !== nextState.rowCount) return true;
    if (widthOfList !== nextState.widthOfList) return true;
    return false;
};

const hasPropsChanged = (props, nextProps) => {
    const { asGrid, children } = props;
    if (asGrid !== nextProps.asGrid) return true;
    if (!isDeepEqual(children, nextProps.children)) return true;
    return false;
};

const shouldRenderBindingLine = (ix, rowCount, numElements) =>
    (ix % rowCount) + 1 !== rowCount && (ix + 1) !== numElements;


class Grid extends React.Component {
    constructor(props) {
        super(props);
        this.updateDimensions = this.updateDimensions.bind(this);
        this.state = {
            columnCount: undefined,
            rowCount: undefined,
            widthOfList: undefined,
        };
    }

    componentDidMount() {
        window.addEventListener('resize', this.updateDimensions);
        const { children, minimumRows } = this.props;
        if (children.length) {
            const widthOfList = getRowContentWidth(this.columnsDivRef);
            const widthOfContainer = getContainerWidth(this.containerDivRef);

            const { columnCount, rowCount } = calculateGrid(
                widthOfList,
                widthOfContainer,
                children,
                minimumRows,
            );
            // NOTE (stelarss):
            // From react/no-did-mount-set-state documentation
            // "Updating the state after a component mount will trigger a second render()"
            // But in this case is a second render the thing we want
            //
            // eslint-disable-next-line react/no-did-mount-set-state
            this.setState({
                widthOfList,
                columnCount,
                rowCount,
            });
        }
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (hasStateChanged(this.state, nextState)) return true;
        if (hasPropsChanged(this.props, nextProps)) return true;
        return false;
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateDimensions);
    }

    updateDimensions() {
        if (this.containerDivRef) {
            const { widthOfList } = this.state;
            const { children, minimumRows } = this.props;

            const { columnCount, rowCount } = calculateGrid(
                widthOfList,
                getContainerWidth(this.containerDivRef),
                children,
                minimumRows,
            );
            this.setState({
                widthOfList,
                columnCount,
                rowCount,
            });
        }
    }

    renderAsGrid(columnCount, rowCount, children) {
        const { shouldAddBinder, rowGap } = this.props;
        const columnsStyle = getGridColumnsStyle(columnCount, rowCount, rowGap);
        const items = children.map((c, ix) => (
            <div
                style={getGridCellStyle(ix, rowCount)}
                ref={(r) => { this.rowItemDivRef = r; }}
                key={ix}
                >
                {shouldAddBinder && shouldRenderBindingLine(ix, rowCount, children.length)
                    && <div style={style.grid.bindingLine} />
                }
                {c}
            </div>
        ));

        items.push(...getGridGaps(children.length, columnCount, rowCount));

        return (
            <div
                style={columnsStyle}
                ref={(r) => { this.columnsDivRef = r; }}
                >
                { items }
            </div>
        );
    }

    renderAsDivs(columnCount, rowCount, children) {
        const chunks = chunk(children, rowCount || children.length);

        const content = chunks.map((c, ix) => (
            <div style={getDivColumnStyle(columnCount)}key={ix}>{c}</div>
        ));

        return (
            <div
                style={style.divs.columns}
                ref={(r) => { this.columnsDivRef = r; }}
                >
                { content }
            </div>
        );
    }

    render() {
        const { children, dataHandle, asGrid } = this.props;
        const { columnCount, rowCount } = this.state;
        if (children.length < 1) {
            return null;
        }

        let content;

        if (asGrid && !isIE11()) {
            content = this.renderAsGrid(columnCount, rowCount, children);
        } else {
            content = this.renderAsDivs(columnCount, rowCount, children);
        }

        return (
            <div
                ref={(c) => { this.containerDivRef = c; }}
                data-handle={dataHandle}
                style={style.grid.container}
                >
                {content}
            </div>
        );
    }
}

Grid.propTypes = {
    asGrid: PropTypes.bool,
    children: PropTypes.arrayOf(Object).isRequired,
    dataHandle: PropTypes.string,
    minimumRows: PropTypes.number,
    rowGap: PropTypes.string,
    shouldAddBinder: PropTypes.bool,
};

Grid.defaultProps = {
    asGrid: true,
    minimumRows: 3,
};

export default (Grid);

export {
    calculateGrid as _calculateGrid,
    getContainerWidth as _getContainerWidth,
    getGridGaps as _getGridGaps,
    getRowContentWidth as _getRowContentWidth,
    hasPropsChanged as _hasPropsChanged,
    hasStateChanged as _hasStateChanged,
    shouldRenderBindingLine as _shouldRenderBindingLine,
};
