import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';

const style = {
    p: {
        fontSize: '0.8rem',
        color: '#333333',
        position: 'relative',
        bottom: '0.5rem',
        left: '2rem',
        backgroundColor: '#ffffff',
        display: 'inline-block',
        padding: '0rem 0.5rem 0rem 0.5rem',
    },
    hr: {
        border: 'none',
        height: '1px',
        backgroundColor: '#333333',
        margin: '0.5rem',
        position: 'relative',
        top: '0.4rem',
    },
    divs: {
        columns: {
            display: 'flex',
        },
    },
    grid: {
        container: {
            overflow: 'visible',
            margin: '1rem 0',
        },
        columns: {
            overflow: 'visible',
            display: 'grid',
            gridAutoFlow: 'column',
        },
        columnItem: {
            WebkitColumnBreakInside: 'avoid',
            pageBreakInside: 'avoid',
            breakInside: 'avoid',
            display: 'flex',
            backgroundColor: 'inherit',
            position: 'relative',
        },
        bindingLine: {
            backgroundColor: defaultStyles.backgroundColor1,
            bottom: '-17px',
            height: '1.1rem',
            left: '50%',
            position: 'absolute',
            width: '0.125rem',
        },
    },
};

const getGridColumnGapStyle = (columnIndex, rowCount) => {
    if (!columnIndex) return {};
    if (!rowCount) return {};

    return {
        gridArea: `1 / ${columnIndex} / ${rowCount + 1} / ${columnIndex}`,
    };
};

const getGridColumnsStyle = (columnCount, rowCount, rowGap = 'normal') => {
    if (!(columnCount && rowCount)) {
        return { display: 'flex' };
    }

    return {
        ...style.grid.columns,
        gridTemplateRows: `repeat(${rowCount}, auto)`,
        gridTemplateColumns: [...Array(columnCount)].map(() => '1fr').join(' 1rem '),
        rowGap,
    };
};

const calcGridArea = (ix, rowCount) => {
    const colIx = ((Math.floor(ix / rowCount) * 2) + 1);
    const rowIx = (ix % rowCount) + 1;

    return `${rowIx} / ${colIx} / ${rowIx} / ${colIx}`;
};

const getGridCellStyle = (ix, rowCount) => ({
    ...style.grid.columnItem,
    gridArea: calcGridArea(ix, rowCount),
});

const getDivColumnStyle = (columnCount) => {
    if (!columnCount || columnCount === 1) return undefined;

    const width = Math.round(100 / columnCount);
    return {
        width: `${width}%`,
    };
};

export {
    style,
    calcGridArea as _calcGridArea,
    getDivColumnStyle,
    getGridCellStyle,
    getGridColumnsStyle,
    getGridColumnGapStyle,
};
