import React from 'react';
import PropTypes from 'prop-types';
import Link from '../../Link';
import styles from './ListStyles';
import {
    truncateLongString,
} from '@webui-one/core/utils/string';

const style = styles();

const Key = ({ LinkComponent, to, value = '' }) => {
    const text = truncateLongString(value);
    return <LinkComponent style={style.list.row.link} to={to}>{text}</LinkComponent>;
};

Key.propTypes = {
    LinkComponent: PropTypes.elementType,
    to: PropTypes.string.isRequired,
    value: PropTypes.string,
};

Key.defaultProps = {
    LinkComponent: Link,
};

export default Key;
