import React from 'react';
import PropTypes from 'prop-types';

import InputComboBox from './InputComboBox';

const filterOptions = (options, value) =>
    options.filter(o => o.value.toLowerCase().includes(value.toLowerCase()));

class ComboBox extends React.Component {
    constructor(props) {
        super(props);
        this.onBlur = this.onBlur.bind(this);
        this.onCancel = this.onCancel.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onFocus = this.onFocus.bind(this);
        this.onSetValue = this.onSetValue.bind(this);
        this.state = {
            draftValue: props.value,
            filterValue: '',
            hasFocus: false,
        };
    }

    static getDerivedStateFromProps(props, state) {
        const { hasFocus } = state;
        const { isLoading, value } = props;
        if (!hasFocus && !isLoading) {
            return { draftValue: value };
        }
        return null;
    }

    // e.g user has changed the value
    onChange(e) {
        this.setState({
            draftValue: e.target.value,
            filterValue: e.target.value,
        });
    }

    onSetValue(newValue) {
        const { onSetValue, value } = this.props;
        if (value !== newValue) {
            onSetValue(newValue);
            this.setState({
                draftValue: newValue,
                filterValue: '',
            });
        }
    }

    onCancel(e) {
        const { value } = this.props;
        this.setState({
            draftValue: value,
        },
        () => {
            e.target.blur();
        });
    }

    onFocus() {
        this.setState({
            hasFocus: true,
            filterValue: '',
        });
    }

    onBlur() {
        this.setState({
            hasFocus: false,
        });
    }

    render() {
        const { options } = this.props;
        const { filterValue, draftValue } = this.state;
        return (<div>
            <InputComboBox
                {...this.props}
                value={draftValue}
                onBlur={this.onBlur}
                onCancel={this.onCancel}
                onChange={this.onChange}
                onFocus={this.onFocus}
                onSetValue={this.onSetValue}
                options={filterOptions(options, filterValue)}
            />
        </div>);
    }
}

ComboBox.propTypes = {
    isLoading: PropTypes.bool,
    onSetValue: PropTypes.func,
    options: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.string,
        text: PropTypes.string,
    })),
    value: PropTypes.string,
};

ComboBox.defaultProps = {
    options: [{
        value: '',
        text: 'empty',
    }],
    value: '',
};

export default ComboBox;

export {
    ComboBox as _ComboBox,
    filterOptions as _filterOptions,
};
