import React from 'react';
import PropTypes from 'prop-types';
import CheckBox from '../CheckBox/CheckBox';
import style from './SelectStyles';

const onChange = (selectedValues, option, e) => {
    if (e !== undefined) {
        selectedValues[option] = e; // eslint-disable-line no-param-reassign
    } else {
        selectedValues[option] = false; // eslint-disable-line no-param-reassign
    }

    return Object.keys(selectedValues)
        .filter(key => selectedValues[key] === true)
        .join(' ');
};

function MultiSelect({ editable, options, onSetValue, value, isLoading }) {
    const selectedValues = {};
    for (let i = 0; i < options.length; i += 1) {
        selectedValues[options[i]] = false;
    }

    if (value !== '') {
        if (value.includes(' ')) {
            // Space sepparated list
            value.split(' ').forEach((data) => {
                selectedValues[data] = true;
            });
        } else if (value.includes(',')) {
            // Comma sepparated list
            value.split(',').forEach((data) => {
                selectedValues[data] = true;
            });
        } else {
            // Single value checked
            selectedValues[value] = true;
        }
    }

    return (
        <div style={editable ? style.wrapper : style.disabledWrapper}>
            {options.map((option, ix) => (
                <div key={`checkbox-wrapper-${ix}`} style={style.row} data-handle={option}>
                    <CheckBox
                        name={`${option}-checkbox`}
                        disabled={!editable || isLoading}
                        value={selectedValues[option]}
                        onSetValue={e => (onSetValue(onChange(selectedValues, option, e)))}
                        style={editable ? style.select : style.disabledSelect}
                        key={`checkbox-${ix}`}
                    />
                    <span key={`checkbox-span-${ix}`} style={style.span}>
                        {option}
                    </span>
                </div>
            ),
            )}
        </div>
    );
}

MultiSelect.propTypes = {
    editable: PropTypes.bool,
    options: PropTypes.arrayOf(PropTypes.string).isRequired,
    onSetValue: PropTypes.func,
    value: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.bool,
    ]),
    isLoading: PropTypes.bool,
};

MultiSelect.defaultProps = {
    options: [],
    value: '',
    onSetValue: () => null,
};

export default MultiSelect;

export {
    onChange as _onChange,
};
