import React from 'react';
import PropTypes from 'prop-types';
import AddSymbol from '@webui-one/styles/static/AddSymbol';
import styles from './EmptyLeafStyles';


function EmptyLeaf({ onCreate, exists, editable }) {
    return (
        <div style={styles.container}>
            {!exists && editable ?
                <button data-handle="empty-leaf-create" style={styles.btn} onClick={onCreate}>
                    <AddSymbol color="#ffffff" />
                </button>
                :
                <span data-handle="empty-leaf-exists" />
            }
        </div>
    );
}

EmptyLeaf.propTypes = {
    onCreate: PropTypes.func.isRequired,
    exists: PropTypes.bool,
    editable: PropTypes.bool,
};

EmptyLeaf.defaultProps = {
    onCreate: () => (null),
};

export default EmptyLeaf;
