import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';

const generateStatusStyles = baseStyle => ({
    unknown: {
        ...baseStyle,
        backgroundColor: defaultStyles.color15,
    },
    'not-reached': {
        ...baseStyle,
        backgroundColor: defaultStyles.color6,
    },
    reached: {
        ...baseStyle,
        backgroundColor: defaultStyles.color14,
    },
    failed: {
        ...baseStyle,
        backgroundColor: defaultStyles.color5,
    },
});

const circle = {
    borderRadius: '50%',
    display: 'flex',
    width: '50px',
    height: '50px',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    whiteSpace: 'nowrap',
    flexShrink: 0,
    color: defaultStyles.backgroundColor1,
};

const state = {
    ...circle,
    backgroundColor: defaultStyles.color6,
    flexShrink: '0',
    width: '1.5rem',
    height: '1.5rem',
};

const component = {
    ...circle,
    width: '3rem',
    height: '3rem',
};

const plan = {
    ...circle,
    width: '6rem',
    height: '6rem',
};

const line = {
    flexGrow: '1',
    height: '3px',
    backgroundColor: 'red',
    margin: '1.5rem 0.25rem',
    minWidth: '5.5rem',
    borderRadius: '1.5px',
};

const label = {
    paddingTop: '0.75rem',
    fontSize: '0.7rem',
    color: defaultStyles.color1,
    width: '7rem',
    alignSelf: 'center',
    textAlign: 'center',
    height: '4rem',
    overflow: 'auto',
    lineHeight: '1.5',
};

const componentsWrapper = {
    flexGrow: '1',
    marginLeft: '-1.5rem',
};

const style = {
    wrapperWrapper: {
        padding: '0 0.7rem',
    },
    planWrapper: {
        display: 'flex',
        paddingTop: '1rem',
        overflow: 'auto',
        position: 'relative',
    },
    arcsWrapper: {
        flexShrink: 0,
        marginTop: '1.5rem',
    },
    componentsWrapper,
    componentsWrapperSingle: {
        ...componentsWrapper,
        marginTop: '2.75rem',
        marginBottom: '0.75rem',
    },
    componentWrapper: {
        display: 'flex',
        flexDirection: 'row',
        height: '6rem',
    },
    componentStatusWrapper: {
        textDecoration: 'none',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        flexGrow: '0',
        flexShrink: '0',
        width: '3rem',
    },
    statesWrapper: {
        display: 'flex',
        justifyContent: 'space-between',
        paddingRight: '3rem',
        flexGrow: '1',
        flexWrap: 'nowrap',
    },
    stateWrapper: {
        width: '1.5rem',
        display: 'flex',
        marginTop: '0.8rem',
        flexDirection: 'column',
        textDecoration: 'none',
    },
    label,
    firstLabel: {
        ...label,
        paddingTop: '0.5rem',
    },
    line,
    firstLine: {
        ...line,
        flexGrow: '0',
        width: '4.5rem',
        marginTop: '1.5rem',
    },
    loading: {
        alignItems: 'center',
        backgroundColor: 'rgba(255, 255, 255, 0.7)',
        display: 'flex',
        height: '100%',
        justifyContent: 'center',
        left: '0',
        position: 'absolute',
        top: '0',
        width: '100%',
        zIndex: 0,
    },
    loader: {
        color: defaultStyles.color9,
    },
    emptyMessage: {
        fontSize: defaultStyles.medium2,
        color: defaultStyles.color6,
        textAlign: 'center',
        marginBottom: '1rem',
        width: '100%',
    },
    statesStatus: generateStatusStyles(state),
    planStatus: generateStatusStyles(plan),
    componentStatus: generateStatusStyles(component),

};

// Used to support arbitrary status (unknown fallback)
const createGetStyle = type => status => (
    status in style[type] ? style[type][status] : style[type].unknown
);

export const getStateStyle = createGetStyle('statesStatus');
export const getComponentStyle = createGetStyle('componentStatus');
export const getPlanStyle = createGetStyle('planStatus');
export const setStatus = (stl, status) => {
    if (status === 'not-reached') return { ...stl, backgroundColor: defaultStyles.color6 };
    if (status === 'reached') return { ...stl, backgroundColor: defaultStyles.color14 };
    if (status === 'failed') return { ...stl, backgroundColor: defaultStyles.color5 };
    return { ...stl, backgroundColor: defaultStyles.color7 };
};

export default style;
