import React from 'react';
import PropTypes from 'prop-types';
import style from './TextAreaStyles';

class TextArea extends React.Component {
    constructor(props) {
        super(props);
        this.handleKeyDown = this.handleKeyDown.bind(this);
        this.onFocus = this.onFocus.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onSetValue = this.onSetValue.bind(this);
        this.state = {
            hasFocus: false,
            isModified: false,
            localNewValue: props.value,
        };
    }

    // eslint-disable-next-line camelcase, react/sort-comp
    UNSAFE_componentWillMount() {
        this.setState({ hasFocus: false });
    }

    // eslint-disable-next-line camelcase, react/sort-comp
    UNSAFE_componentWillReceiveProps(nextProps) {
        const { value: newValue, error } = nextProps;
        const { hasFocus } = this.state;

        if (!hasFocus && (this.props.value === this.state.localNewValue || error)) {
            this.setState({ localNewValue: newValue });
        }
    }

    shouldComponentUpdate(nextProps, nextState) {
        const { editable, error, isDefaultValue, isLoading } = nextProps;
        const { hasFocus, localNewValue } = nextState;
        if (isDefaultValue !== this.props.isDefaultValue) return true;
        if (editable !== this.props.editable) return true;
        if (error !== this.props.error) return true;
        if (isLoading !== this.props.isLoading) return true;
        if (localNewValue !== this.state.localNewValue) return true;
        if (hasFocus !== this.state.hasFocus) return true;
        return false;
    }

    onFocus() {
        this.setState({ hasFocus: true, isModified: false, localNewValue: this.props.value });
    }

    onSetValue(e) {
        const { onSetValue, value } = this.props;
        const { localNewValue, isModified } = this.state;
        if (localNewValue !== value || isModified) {
            onSetValue(e.target.value);
        }
        this.setState({ hasFocus: false, isModified: false });
    }

    onChange(e) {
        const { onChange } = this.props;
        onChange(e.target.value);
        this.setState({ localNewValue: e.target.value, isModified: true });
    }

    handleKeyDown(e) {
        e.persist();
        if (e.key === 'Escape') {
            this.setState({
                localNewValue: this.props.value,
                isModified: false,
            }, () => {
                e.target.blur();
            });
        }
    }

    textAreaStyle() {
        const { editable, error, isOperdata } = this.props;
        if (!editable) {
            return isOperdata ? style.textAreaReadOnlyOperdata : style.textAreaReadOnly;
        }
        if (error) return style.textAreaError;
        return style.textArea;
    }

    render() {
        const {
            name,
            editable,
            isDefaultValue,
            autoFocus } = this.props;
        const { hasFocus, localNewValue } = this.state;
        const localNewValueDefault = (isDefaultValue) && (!hasFocus)
            ? `(${localNewValue})`
            : localNewValue;
        const textAreaStyle = this.textAreaStyle();
        return (
            <textarea
                autoFocus={autoFocus}
                data-handle={name}
                readOnly={!editable}
                style={textAreaStyle}
                value={localNewValueDefault}
                onChange={this.onChange}
                onFocus={this.onFocus}
                onBlur={this.onSetValue}
                onKeyDown={this.handleKeyDown}
            />
        );
    }
}

TextArea.propTypes = {
    autoFocus: PropTypes.bool,
    editable: PropTypes.bool,
    error: PropTypes.shape(Object),
    isDefaultValue: PropTypes.bool,
    isLoading: PropTypes.bool,
    isOperdata: PropTypes.bool,
    name: PropTypes.string,
    onChange: PropTypes.func,
    onSetValue: PropTypes.func,
    value: PropTypes.string,
};

TextArea.defaultProps = {
    value: '',
    isDirty: false,
    onChange: () => (null),
    onSetValue: () => (null),
};

export default TextArea;
