export const getDevices = ({
    chunkSize,
    offset,
    columnKey,
    sortorder,
    xpathExpression,
    selection = QUERY_SELECTION,
    funcs: {
        getSchema = _getSchema,
        query = _query,
    } = {},
} = {}) => dispatch => dispatch(getSchema({
    path: devicesRootPath,
    params: {
        levels: -1,
        stop_on_list: true,
    },
    reload: true,
})).then(() => {
    const querySelection = uniq([
        'name',
        ...selection,
    ]);
    return dispatch(query({
        selection: querySelection,
        path: deviceListPath,
        sort: columnKey,
        sortOrder: sortorder,
        initialOffset: offset,
        chunkSize,
        result_as: 'string',
        xpathExpr: xpathExpression,
        contextNode: devicesRootPath,
    }));
});

export const loadDeviceGroups = ({
    funcs: {
        query = _query,
    } = {},
} = {}) => (
    (dispatch) => {
        dispatch(loadDeviceGroupsStartAction());
        return dispatch(query({ selection: ['name'], path: deviveGroupListPath }))
            .then((groups) => {
                if (groups.result.results.length === 0) return Promise.resolve(undefined);
                return Promise.all(groups.result.results.map(r => dispatch(query({
                    selection: undefined,
                    path: getDeviceGroupDeviceListPath(r.name),
                }))));
            })
            .then(result => (
                dispatch(loadDeviceGroupsCompleteAction(result))
            )).catch(error => (
                dispatch(loadDeviceGroupsErrorAction(error))
            ));
    }
);

export const getValueFromResult = (result) => {
    if (!result || !result.result || !result.result.results) return [];
    return result.result.results.reduce((acc, obj) => [...acc, ...Object.values(obj)], []);
};

const addValues = (dispatch, group, valuesToAdd, {
    funcs: {
        createAllowExistsThunk = _createAllowExistsThunk,
    } = {},
} = {}) => {
    const path = getDeviceGroupDeviceListPath(group);
    return dispatch(_query({ selection: undefined, path })).then((result) => {
        const value = getValueFromResult(result);
        const minValuesToAdd = difference(valuesToAdd, value);
        if (isEmpty(minValuesToAdd)) return Promise.resolve(value);
        const createOperations = minValuesToAdd.map(d =>
            dispatch(createAllowExistsThunk({
                path: getDeviceGroupDeviceListDevicePath(group, d),
            })),
        );
        return Promise.all(createOperations).then(() => union(valuesToAdd, value));
    });
};

export const addDevicesToGroup = (devices, group, {
    funcs: {
        addValuesFunc = addValues,
        createAllowExistsThunk = _createAllowExistsThunk,
        loadDeviceGroupsFunc = loadDeviceGroups,
    } = {},
} = {}) => (
    (dispatch) => {
        dispatch(addDevicesToGroupStartAction(group));
        const groupPath = getDeviceGroupDeviceListPath(group);
        return dispatch(createAllowExistsThunk({ path: groupPath }))
            .then(() => addValuesFunc(dispatch, group, devices))
            .then(() => dispatch(loadDeviceGroupsFunc()))
            .then(() => dispatch(addDevicesToGroupCompleteAction(group)))
            .catch(error => (
                dispatch(addDevicesToGroupErrorAction(group, error))
            ));
    }
);

export const removeDevicesFromGroup = (devices, group, {
    funcs: {
        deleteNodesThunk = _deleteNodesThunk,
        query = _query,
    } = {},
} = {}) => (
    (dispatch) => {
        dispatch(removeDevicesFromGroupStartAction(group));
        const path = getDeviceGroupDeviceListPath(group);
        return dispatch(query({ selection: undefined, path })).then((result) => {
            const value = getValueFromResult(result);
            const valuesToRm = intersect(value, devices);
            return dispatch(deleteNodesThunk({ paths: valuesToRm.map(d => `${path}{${d}}`) }));
        }).then((value) => {
            if (value.length > 0 && value[0].error) throw value[0].error;
            return dispatch(removeDevicesFromGroupCompleteAction(group, value));
        }).catch(error => (
            dispatch(removeDevicesFromGroupErrorAction(group, error))
        ));
    }
);

const deviceRE = /^(\/ncs:devices\/device\{([^}]+)\}).*/;

export const loadModules = (configPath, {
    funcs: {
        readThunk = read,
        query = jsonrpcQuery,
    } = {},
} = {}) => (
    (dispatch) => {
        const match = deviceRE.exec(configPath);
        if (!match) return Promise.resolve();
        const path = `${match[1]}/module`;
        dispatch(loadModulesStartAction(path));

        return dispatch(readThunk()).then(({ th }) => (
            query({ th, selection: ['name'], path })
        )).then(result => (
            dispatch(loadModulesCompleteAction(
                path,
                // NOTE(dbildstr): return array of strings instead of array of array of strings
                result.results.map(module => module[0]),
            ))
        )).catch(error => (
            dispatch(loadModulesErrorAction(path, error))
        ));
    }
);

export const addDevice = ({
    parentPath,
    keyValues,
    chunkSize,
    offset,
    columnKey,
    sortorder,
    xpathExpression,
    funcs: {
        createDisallowExistsThunk = _createDisallowExistsThunk,
        getDevicesThunk = getDevices,
    } = {},
}) => (
    (dispatch) => {
        const validKeyValue = quoteKey(keyValues.name.value);
        const path = `${parentPath}{${validKeyValue}}`;
        return dispatch(createDisallowExistsThunk({ path })).then((result) => {
            if (result.error) return result;
            return dispatch(
                getDevicesThunk({
                    chunkSize,
                    offset,
                    columnKey,
                    sortorder,
                    xpathExpression,
                }),
            );
        });
    }
);
