// eslint-disable-next-line import/prefer-default-export
export const isBigYangPath = (path) => {
    if (/^\/ncs:devices\/device\{[^}]+\}.*$/.test(path)) {
        return true;
    }
    if (path && path.startsWith('/ncs:devices/template')) {
        return true;
    }
    return false;
};

export const isPackagePath = path => path.startsWith('/ncs:packages/package{');
export const isDeviceRootPath = path => /^\/ncs:devices\/device\{[^}]+\}$/.test(path);
