import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router';
import InformationButton from '@webui-one/components/InformationButton/InformationButton';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import ActionSymbol from '@webui-one/styles/static/ActionSymbol';
import { overlayStyle, actionStyle as style } from '@webui-one/styles/LeafStyles';

const ActionLink = ({ info, isEditMode, isOperdata, label, to }) => (<div
    data-handle={label}
    style={style.actionContainer}
    >
    <div style={style.wrapperWrapper}>
        <Link
            to={to}
            style={isOperdata ? style.actionWrapperOperdata : style.actionWrapper}
                >
            <div style={style.symbol}>
                <ActionSymbol color={defaultStyles.color3} />
            </div>
            <div style={style.label}>{label}</div>
        </Link>
        {isEditMode && <div style={overlayStyle.editModeOverlay} data-handle="overlay">
            <div style={overlayStyle.symbol}>
                {info.length > 0 &&
                    <InformationButton color={defaultStyles.color3} info={info} />}
            </div>
            </div>}
    </div>
</div>);

ActionLink.propTypes = {
    info: PropTypes.arrayOf(PropTypes.object),
    label: PropTypes.string,
    to: PropTypes.string,
    isEditMode: PropTypes.bool,
    isOperdata: PropTypes.bool,
};

export default ActionLink;
