import React from 'react';
import PropTypes from 'prop-types';
import {
    isObject,
} from '@webui-one/core/utils/object';
import { operIndicatorStyle, actionIndicatorStyle } from './ContentIndicatorsStyles';
import ContentIndicator from './ContentIndicator';

const ContentIndicators = ({ schemaIncludes }) => {
    if (!isObject(schemaIncludes)) return false;
    if (schemaIncludes.oper === false &&
        schemaIncludes.actions === false
    ) {
        return false;
    }

    const content = [];

    if (schemaIncludes.oper) {
        content.push(
            <ContentIndicator
                key={2}
                direction={'right'}
                toolTipText={'There is operational data on this page'}
                toolTipStyle={operIndicatorStyle}
            />,
        );
    }

    if (schemaIncludes.actions) {
        content.push(
            <ContentIndicator
                key={3}
                direction={'right'}
                toolTipText={'There are actions on this page'}
                toolTipStyle={actionIndicatorStyle}
            />,
        );
    }

    return (
        <div style={{ display: 'flex' }}>
            <React.Fragment>
                {content}
            </React.Fragment>
        </div>
    );
};

ContentIndicators.propTypes = {
    schemaIncludes: PropTypes.shape({
        oper: PropTypes.bool.isRequired,
        actions: PropTypes.bool.isRequired,
    }),
};

ContentIndicators.defaultProps = {
};

export default ContentIndicators;
