import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router';

import InformationButton from '@webui-one/components/InformationButton/InformationButton';
import TrashcanSymbol from '@webui-one/styles/static/TrashcanSymbol';
import PresenceButton from 'Common/Components/ConfigRender/PresenceButton/PresenceButton';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import style from './SchemaHeaderStyles';

const SchemaHeader = ({
    exists,
    info,
    isConfig,
    isDirty,
    isEditMode,
    isOperdata,
    isPresenceContainer,
    isRootPath,
    link,
    name,
    onCreate,
    onDelete,
    path,
}) => {
    if (isRootPath) return false;

    const shouldDisplayPresenceButton = (
        isPresenceContainer && !exists
    );
    const shouldDisplayTrashcan = isPresenceContainer && isEditMode && exists;
    const shouldDisplayInfoButton = info.length > 0 && isEditMode;
    const disablePresenceButton = (
        !isConfig || !isPresenceContainer || exists === true
    );

    return (
        <div style={style.rootContainer} data-handle={name}>
            <div style={isOperdata ? style.linkWrapperOper : style.linkWrapper}>
                {isDirty && (<div style={style.dot} data-handle="dot" />)}
                {shouldDisplayPresenceButton && (
                    <div style={style.button}>
                        <PresenceButton
                            key={`${path}-presence-button`}
                            path={path}
                            disabled={disablePresenceButton}
                            onCreate={onCreate}
                    />
                    </div>
                )}
                {shouldDisplayTrashcan && <button
                    style={style.button}
                    onClick={onDelete}
                    data-handle="deleteContainer">
                    <TrashcanSymbol color={defaultStyles.color1} />
                </button>}
                {shouldDisplayInfoButton && <div
                    style={style.button}
                    data-handle="infoContainer">
                    <InformationButton color={defaultStyles.color1} info={info} />
                </div>}
                <Link style={style.textLink} to={link}>{`${name}/`}</Link>
            </div>
            <div style={style.line}/>
        </div>
    );
};

SchemaHeader.propTypes = {
    exists: PropTypes.bool,
    info: PropTypes.arrayOf(PropTypes.object),
    isConfig: PropTypes.bool,
    isDirty: PropTypes.bool,
    isEditMode: PropTypes.bool,
    isOperdata: PropTypes.bool,
    isPresenceContainer: PropTypes.bool,
    isRootPath: PropTypes.bool,
    link: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    onCreate: PropTypes.func,
    onDelete: PropTypes.func,
    path: PropTypes.string.isRequired,
};

SchemaHeader.defaultProps = {
    dirtyRefCount: 0,
    exists: false,
    info: [],
    isPresenceContainer: false,
    dirty: false,
};

export default SchemaHeader;
