import React from 'react';
import PropTypes from 'prop-types';

import style from './DateAndTimeStyles';

const Suggestions = ({
    showSuggestions,
    draftValue,
    suggestions,
    activeSuggestionIx,
}) => {
    if (showSuggestions !== true) return null;
    if (suggestions === undefined) return null;

    const items = suggestions.map((s, ix) => {
        if (ix === activeSuggestionIx) {
            return (
                <li
                    data-handle="active-suggestion"
                    key={ix}
                    style={style.suggestionActive}
                    >{`${draftValue}${s}`}</li>
            );
        }
        return <li key={ix} style={style.suggestion}>{`${draftValue}${s}`}</li>;
    });

    return (
        <ul style={style.listbox}>{ items }</ul>
    );
};

Suggestions.propTypes = {
    showSuggestions: PropTypes.bool,
    draftValue: PropTypes.string,
    suggestions: PropTypes.arrayOf(PropTypes.string),
    activeSuggestionIx: PropTypes.number,
};

export default Suggestions;

