import React from 'react';
import PropTypes from 'prop-types';
import MessageBox from '@webui-one/components/ConfigRender/MessageBox';
import style from '@webui-one/components/ConfigRender/MessageBoxStyles';


const NoData = props => (
    <MessageBox name="no-data-box">
        <React.Fragment>
            All content in this view is hidden.
            <button
                style={style.button}
                onClick={props.onClick}
                data-handle="show-everything-button"
            >Show all</button>
        </React.Fragment>
    </MessageBox>
);

NoData.propTypes = {
    onClick: PropTypes.func,
};

export default NoData;
