// Implemented according to
import React from 'react';
import PropTypes from 'prop-types';
import { isFunction } from '@webui-one/core/utils/functional';
import Link from '../../Link';
import style from './InputComboBoxStyles';
import { getNodePathWithKeys } from '@webui-one/core/keypath/kp-parser';


class InputComboBox extends React.Component {
    constructor(props) {
        super(props);
        this.inputRef = undefined;
        this.onKeyDown = this.onKeyDown.bind(this);
        this.onMouseDown = this.onMouseDown.bind(this);
        this.shouldRenderOptions = this.shouldRenderOptions.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onFocus = this.onFocus.bind(this);
        this.onBlur = this.onBlur.bind(this);
        this.optionClicked = this.optionClicked.bind(this);
        this.state = {
            littleArrowClicked: false,
            dropDownIndex: -1,
            userTyping: false,
        };
    }

    onChange(e) {
        this.props.onChange(e);
        this.setState({
            dropDownIndex: -1,
            userTyping: true,
        });
    }

    onFocus(e) {
        const { onFocus } = this.props;
        onFocus(e);
        this.setState({
            dropDownIndex: -1,
        });
        e.target.select();
    }

    onBlur(e) {
        const {
            value,
            onBlur,
            onSetValue,
        } = this.props;

        onSetValue(value);
        this.setState({
            dropDownIndex: -1,
            userTyping: false,
            littleArrowClicked: false,
        });
        onBlur(e);
    }

    onKeyDown(e) {
        const { onCancel, onSetValue, value, options } = this.props;
        const { dropDownIndex } = this.state;

        e.persist();

        switch (e.key) {
        case 'Enter': {
            const newValue = dropDownIndex > -1 ? options[dropDownIndex].value : value;
            onSetValue(newValue);
            this.setState({
                dropDownIndex: -1,
                userTyping: false,
                littleArrowClicked: false,
            });

            if (dropDownIndex > -1) {
                e.stopPropagation();
            }
            break;
        }
        case 'Escape': {
            if (dropDownIndex !== -1) {
                this.setState({
                    dropDownIndex: -1,
                    userTyping: false,
                    littleArrowClicked: false,
                });
                e.stopPropagation();
            } else {
                onCancel(e);
            }
            break;
        }
        case 'ArrowUp':
        case 'ArrowDown': {
            const diff = e.key === 'ArrowUp' ? -1 : 1;
            this.setState({
                dropDownIndex: (dropDownIndex + diff + options.length) % options.length,
            });
            e.preventDefault();
            break;
        }
        default:
            break;
        }
    }

    onMouseDown(e) {
        e.preventDefault();
        this.inputRef.focus();
        this.setState({
            littleArrowClicked: !this.shouldRenderOptions(),
            dropDownIndex: -1,
            userTyping: false,
        });
    }

    optionClicked(value) {
        const { onSetValue } = this.props;
        onSetValue(value);
        this.setState({
            dropDownIndex: -1,
            userTyping: false,
            littleArrowClicked: false,
        });
    }

    shouldRenderOptions() {
        const { options, editable } = this.props;
        const { dropDownIndex, littleArrowClicked, userTyping } = this.state;
        return !!(editable &&
                  options.length &&
                  (userTyping || dropDownIndex !== -1 || littleArrowClicked)
        );
    }

    formatDepsTarget(dep) {
        const { formatDepsTarget } = this.props;
        if (!isFunction(formatDepsTarget)) {
            return dep;
        }
        return formatDepsTarget(dep);
    }

    render() {
        const {
            autoFocus,
            editable,
            LinkComponent,
            name,
            options,
            placeholder,
            value,
            deps,
            path,
        } = this.props;
        const {
            dropDownIndex,
        } = this.state;

        const shouldRenderOptions = this.shouldRenderOptions();

        const optionElements = options.slice(0, 16).map(({ value: val, text }, i) => (
            <li
                key={val}
                onMouseDown={e => e.preventDefault()}
                onClick={() => this.optionClicked(val)}
                style={i === dropDownIndex ? style.optionHighlighted : style.option}
                data-handle={`option-${val}`}>
                {text || val}
            </li>
        ));

        if (deps && deps.length > 0) {
            // There will never be more than one dependancy, so we just use the first one.
            const dep = getNodePathWithKeys(path, deps[0]);

            optionElements.push(
                <li
                    key={`dependency-link-${dep}`}
                    data-handle={`dependency-link-${dep}`}
                    onMouseDown={e => e.preventDefault()}
                    style={style.depOption}
                    >
                    <LinkComponent
                        title={dep}
                        style={style.sourceLink}
                        to={this.formatDepsTarget(dep)}
                        >
                        Source
                    </LinkComponent>
                </li>,
            );
        }

        return (
            <div style={style.relative}>
                <div
                    style={style.wrapper}
                    data-handle={`combobox-${name}`}
                >
                    <input
                        autoFocus={autoFocus}
                        onBlur={this.onBlur}
                        onChange={this.onChange}
                        onFocus={this.onFocus}
                        onKeyDown={this.onKeyDown}
                        onMouseDown={this.onMouseDown}
                        placeholder={placeholder}
                        style={editable ? style.inputField : style.inputFieldReadOnly}
                        readOnly={!editable}
                        type="text"
                        value={value}
                        ref={(r) => { this.inputRef = r; }}
                />
                    { options.length > 0 &&
                    <button
                        onMouseDown={this.onMouseDown}
                        style={style.arrow}
                        tabIndex="-1"
                    >&#9662;</button>
                }
                </div>
                { shouldRenderOptions &&
                    <ul style={style.listbox}>
                        { optionElements }
                    </ul>
                }
            </div>
        );
    }
}

InputComboBox.propTypes = {
    autoFocus: PropTypes.bool,
    editable: PropTypes.bool,
    formatDepsTarget: PropTypes.func,
    LinkComponent: PropTypes.elementType,
    name: PropTypes.string.isRequired,
    onBlur: PropTypes.func,
    onChange: PropTypes.func,
    onCancel: PropTypes.func,
    onFocus: PropTypes.func,
    onSetValue: PropTypes.func,
    options: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.string,
        text: PropTypes.string,
    })),
    placeholder: PropTypes.string,
    value: PropTypes.string,
    deps: PropTypes.arrayOf(PropTypes.string),
    path: PropTypes.string,
};

InputComboBox.defaultProps = {
    autoFocus: false,
    options: [],
    info: [],
    LinkComponent: Link,
    value: '',
    isDirty: false,
    onSetValue: () => (null),
    onChange: () => (null),
    onBlur: () => (null),
    onFocus: () => (null),
};

export default InputComboBox;
