import { quoteKey } from '@webui-one/core/functional_utils';
import {
    getKeyValue,
    getQuerySelection,
    isVisibleColumn,
} from '@webui-one/core/utils/list';
import { escapeKey } from '@webui-one/core/utils/path';
import { sequence } from '@webui-one/core/utils/promise';
import { lstrip } from '@webui-one/core/utils/string';
import AddSymbol from '@webui-one/styles/static/AddSymbol';
import RemoveSymbol from '@webui-one/styles/static/RemoveSymbol';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import ModalSchemaInputContainer from 'Common/Components/Modal/ModalSchemaInputContainer';
import {
    createNodeRowSelector,
    selectNode,
    selectDirty,
    selectOffset,
    selectsortOrder,
    selectSortColumn,
} from 'modules/nodes/selectors';
import {
    createDisallowExistsThunk,
    deleteNodesThunk,
} from 'modules/nodes/thunks';
import * as nodesActions from 'modules/nodes/actions';
import * as nodesActionTypes from 'modules/nodes/actionTypes';
import {
    createColumnsVisibilitySelector,
} from 'modules/userdata/selectors';
import {
    selectConfigIsLoading,
    selectTotalNumberOfResults,
} from 'modules/data/selectors';
import { selectApplicationName } from 'modules/application/selectors';
import {
    selectIsEditMode,
    selectShowEmpty,
} from 'ConfigurationEditor/reducer';
import { connect } from 'react-redux';
import React from 'react';
import PropTypes from 'prop-types';
import { Messages, translate } from 'modules/intl';
import {
    selectLocationPathnameFromApplicationName,
} from 'modules/routing/selectors';
import List from '@webui-one/components/ConfigRender/List/List';
import {
    createCheckBoxCellFactory,
    createCheckboxHeaderFactory,
    createDirtyCell,
    createEmptyHeader,
    createHeader as createNonSortableHeader,
    createKeyCellFactory,
    createSortableHeaderFactory,
    getDefaultColumn,
} from '@webui-one/components/ConfigRender/List/ListCells';
import Pagination from '@webui-one/components/ConfigRender/List/Pagination';
import SelectMenu from '@webui-one/components/ConfigRender/List/SelectMenu';
import styles from '@webui-one/components/ConfigRender/List/ListStyles';
import EmptyCellContainer from './EmptyCellContainer';

const LIST_CHUNK_SIZE = 10;
const LEAF_LIST_CHUNK_SIZE = 5;

const style = styles();

const createEmptyCellFactory = column => (name, cellKey, rowKey) =>
    <EmptyCellContainer path={`${rowKey}/${column}`} />;

const buildWidgetLeaf = (child, name, isEmptyLeaf) => {
    if (isEmptyLeaf) {
        return {
            ...child,
            createHeader: getDefaultColumn().createHeader,
            createCell: createEmptyCellFactory(name),
        };
    }
    return child;
};

const buildColsDataFromSchemaChildren = (children, createHeader, createCell) => (
    (children || []).map((c) => {
        const { isEmptyLeaf, kind, name } = c;
        const child = {
            ...getDefaultColumn(),
            title: name,
            key: name,
            createHeader,
            createCell,
        };

        switch (kind) {
        case 'key':
            return {
                ...child,
                canBeHidden: false,
            };
        default:
            return buildWidgetLeaf(child, name, isEmptyLeaf);
        }
    })
);

const validateForm = values => (
    Object.values(values).reduce((acc, value) => (
        acc && value.length > 0
    ), true)
);

const isLinkable = ({ isLeafList }) => !isLeafList;

const createColumn = (key, title, createHeader, createCell) => ({
    ...getDefaultColumn(),
    key,
    canBeHidden: false,
    title,
    createHeader,
    createCell,
    isSticky: true,
});

const createIsDirtyColumn = (createHeader, createCell) => (
    createColumn('isDirty', 'Changed', createHeader, createCell)
);

const createIsSelectedColumn = (createHeader, createCell) => (
    createColumn('isSelected', 'Is selected', createHeader, createCell)
);

const createBaseColumns = (editable, createIsSelectedColumnHeader, createIsSelectedColumnCell) => {
    if (editable) {
        return [
            createIsDirtyColumn(createEmptyHeader, createDirtyCell),
            createIsSelectedColumn(createIsSelectedColumnHeader, createIsSelectedColumnCell),
        ];
    }
    return [];
};

const createColumns = ({
    isLeafList,
    columnChildren,
    createHeader,
    applicationName,
} = {}) => {
    if (isLeafList) {
        const key = 0;
        return [{ ...getDefaultColumn(), title: '', key, createHeader }];
    }

    const createCell = isLinkable({ isLeafList }) ?
        createKeyCellFactory(applicationName, Link) : getDefaultColumn().createCell;

    return buildColsDataFromSchemaChildren(columnChildren, createHeader, createCell);
};

// Input format should be: '[ <space sepparated list> ]'
const isInputFormatValid = (input) => {
    if (input.startsWith('[ ') && !input.endsWith(' ]')) {
        return false;
    } else if (!input.startsWith('[ ') && input.endsWith(' ]')) {
        return false;
    }
    return true;
};

const isSpaceSepparatedList = (input) => {
    if (input.startsWith('[ ') && input.endsWith(' ]')) {
        return true;
    }
    return false;
};

// Matches a string against white spaces
// Doesn't split if surrouded with quotes e.g [ "foo bar" ] is 1 key.
// Doesn't split on unmatched quotes e.g [ Mario's Luigi's ] is correcly split as 2 keys.
const makeSpaceSepparatedList = str => (str.match(/"([^"]*)"|'([^']*)'|[^\s]+/g));

class ListContainer extends React.Component {
    constructor(props) {
        super(props);
        this.onChange = this.onChange.bind(this);
        this.onClose = this.onClose.bind(this);
        this.onConfirm = this.onConfirm.bind(this);
        this.onDeleteSelected = this.onDeleteSelected.bind(this);
        this.onSelectAllRows = this.onSelectAllRows.bind(this);
        this.onSelectRow = this.onSelectRow.bind(this);
        this.showAddListItemModal = this.showAddListItemModal.bind(this);
        this.selectVisibleColumn = this.selectVisibleColumn.bind(this);
        this.changeSortOrder = this.changeSortOrder.bind(this);
        this.onGoToPage = this.onGoToPage.bind(this);
        this.state = {
            formKeyValues: {},
            hasCheckboxes: true,
            hasSelectedAll: false,
            selectMenuIsActive: false,
            shouldShowColumnSelector: false,
            showAddListItemModal: false,
            errorOnCreate: undefined,
        };
    }

    componentDidMount() {
        const {
            onQueryData,
            isActionInput,
            isLeafList,
            isLoading,
            totalNumberOfResults,
        } = this.props;
        if (!isLoading && isActionInput) {
            const chunkSize = this.getChunkSize();
            onQueryData({ chunkSize });
            return;
        }
        if (!isLoading && totalNumberOfResults > 0 && !isLeafList) {
            const chunkSize = this.getChunkSize();
            onQueryData({ chunkSize });
        }
    }

    componentDidUpdate(prevProps) {
        const {
            isModified,
            offset,
            totalNumberOfResults,
            rows,
        } = this.props;
        const {
            isModified: prevModified,
        } = prevProps;
        const { hasSelectedAll } = this.state;
        if (isModified && !prevModified) {
            this.onGoToPage(1);
        }
        if (Array.isArray(rows)) {
            const rowCount = rows.length;
            const selectedCount = rows.filter(r => r.data.isSelected).length;
            if (hasSelectedAll && (rowCount === 0 || selectedCount < rowCount)) {
                // eslint-disable-next-line react/no-did-update-set-state
                this.setState({ hasSelectedAll: false });
            } else if (!hasSelectedAll && rowCount > 0 && selectedCount === rowCount) {
                // eslint-disable-next-line react/no-did-update-set-state
                this.setState({ hasSelectedAll: true });
            }
        }
        if (rows && rows.length === 0 && totalNumberOfResults > 0) {
            this.onGoToPage(Math.max(1, offset - LIST_CHUNK_SIZE));
        }
    }

    onChange(e) {
        const { formKeyValues } = this.state;
        const { value } = e.target;
        const keyName = e.target.dataset.keyName;
        const values = { ...formKeyValues, [keyName]: value };
        this.setState({
            formIsValid: validateForm(values),
            formKeyValues: values,
            formKeyValue: value,
        });
    }

    onClose() {
        this.setState({
            showAddListItemModal: false,
            errorOnCreate: undefined,
        });
    }

    onConfirm(path, items) {
        const {
            isLeafList,
            onDeleteRows,
            onCreate,
            onError,
        } = this.props;

        if (isLeafList) {
            const keyValue = getKeyValue(items, true);
            const chunkSize = this.getChunkSize();

            if (!isInputFormatValid(keyValue)) {
                return onError(path, 'Input format is invalid');
            }

            if (keyValue === '[  ]' || keyValue === '[ ]') {
                return onDeleteRows([path], chunkSize).then(() => {
                    this.onClose();
                });
            }

            if (isSpaceSepparatedList(keyValue)) {
                // Strip starting '[ ' and ending ' ]'
                const keyList = makeSpaceSepparatedList(keyValue.substr(2, keyValue.length - 3));
                if (!keyList) {
                    return onError(path, 'Input format is invalid');
                }

                return onDeleteRows([path], chunkSize).then(() => {
                    const promiseChain = keyList.map(key => (
                        () => (onCreate(`${path}{${quoteKey(key)}}`))
                    ));
                    return sequence(promiseChain).then((results) => {
                        const errors = results.filter((result) => {
                            if (result.type && result.type === nodesActionTypes.CREATE_ERROR) {
                                return true;
                            }
                            return false;
                        });
                        if (errors.length === 0) {
                            this.onClose();
                        }
                    });
                });
            }
        }

        const keyValue = getKeyValue(items, false);
        return onCreate(`${path}{${keyValue}}`).then((result) => {
            if (!result.error) {
                this.onClose();
            } else {
                const error = typeof result.error === 'string' ? result.error : result.error.message;
                this.setState({ errorOnCreate: error });
            }
            return result;
        });
    }

    onDeleteSelected() {
        const { rows, onDeleteRows } = this.props;
        if (!rows) return;
        const selectedRows = rows.filter(row => row.data.isSelected);
        const chunkSize = this.getChunkSize();
        onDeleteRows(selectedRows.map(row => row.rowKey), chunkSize);
    }

    onSelectAllRows(value) {
        const { path, rows, onSetCheckBoxValues } = this.props;
        this.setState({ hasSelectedAll: value });
        if (!rows) return;
        onSetCheckBoxValues(path, rows.map(row => row.rowKey), value);
    }

    onSelectRow(value, keyPath) {
        const { path, onSetCheckBoxValues } = this.props;
        onSetCheckBoxValues(path, [keyPath], value);
    }

    onGoToPage(offset) {
        const {
            onQueryData,
            isLoading,
        } = this.props;

        if (!isLoading) {
            const chunkSize = this.getChunkSize();
            onQueryData({ chunkSize, offset });
        }
    }

    getChunkSize() {
        const { isLeafList } = this.props;
        return isLeafList ? LEAF_LIST_CHUNK_SIZE : LIST_CHUNK_SIZE;
    }

    getAddButton() {
        return (
            <button
                onClick={this.showAddListItemModal}
                style={style.list.header.button}
                data-handle="list-add-button">
                <AddSymbol color="#ffffff" />
            </button>
        );
    }


    getButtons(columns) {
        const { editable, isLeafList, path, rows } = this.props;

        const props = {
            resetHide: this.resetHide,
            initHide: this.initHide,
            hideSelectMenu: this.hideSelectMenu,
            toggleMenuIsActive: this.toggleMenuIsActive,
            onChange: (y, value) => this.selectVisibleColumn(y, value),
            path,
        };

        const empty = !(rows && rows.length > 0);
        const hasSelection = !empty && rows.filter(row => row.data.isSelected).length > 0;
        const stl = style.list.header;
        return (<div key="buttons" style={style.selectColumns.buttonWrapper}>
            {(isLeafList || empty) || <SelectMenu
                {...props}
                columns={columns.filter(c => c.canBeHidden)}
            />}
            {(editable && !empty) &&
            <button
                key={`${path}btn1`}
                data-handle="list-delete-button"
                style={hasSelection ? stl.button : stl.buttonDisabled}
                disabled={!hasSelection}
                onClick={this.onDeleteSelected}
            >
                <RemoveSymbol color={hasSelection ?
                    defaultStyles.color3 : defaultStyles.color6} />
            </button>}
            {(editable && empty) &&
                <div style={style.list.centeredText}>Add list item </div>}
            {editable && this.getAddButton()}
        </div>);
    }

    getColumns() {
        const {
            applicationName,
            columnChildren,
            columnsVisibility,
            editable,
            isLeafList,
            name,
            sortColumn,
            sortOrder,
        } = this.props;
        const { hasSelectedAll } = this.state;
        const legacySort = { columnKey: sortColumn, isDesc: sortOrder === 'descending' };
        const createHeader = !isLeafList
            ? createSortableHeaderFactory(this.changeSortOrder, legacySort)
            : createNonSortableHeader;

        const createSelectedColumnHeader =
            createCheckboxHeaderFactory(hasSelectedAll, this.onSelectAllRows);

        const createSelectedColumnCell = createCheckBoxCellFactory(this.onSelectRow);
        const columns = [
            ...createBaseColumns(editable, createSelectedColumnHeader, createSelectedColumnCell),
            ...createColumns({
                name,
                isLeafList,
                columnChildren,
                createHeader,
                applicationName,
            }),
        ];
        columns.forEach((c, index) => {
            columns[index].isVisible = isVisibleColumn(columnsVisibility, c);
        });
        return columns;
    }

    selectVisibleColumn(key, value) {
        const { onSetColumnVisible, path } = this.props;
        onSetColumnVisible(path, key, value);
    }

    changeSortOrder(columnKey) {
        const {
            onQueryData,
            sortOrder,
            sortColumn,
            isLoading,
        } = this.props;

        if (!isLoading) {
            const isDesc = columnKey === sortColumn && sortOrder !== 'descending';
            const isNone = columnKey === sortColumn && sortOrder === 'descending';

            let newSortorder = 'ascending';
            let newColumnKey = columnKey;
            if (isDesc) newSortorder = 'descending';
            if (isNone) {
                newSortorder = undefined;
                newColumnKey = undefined;
            }

            const offset = 1;
            const chunkSize = this.getChunkSize();

            onQueryData({ chunkSize, offset, sortColumn: newColumnKey, sortOrder: newSortorder });
        }
    }

    showAddListItemModal() {
        this.setState({ showAddListItemModal: true });
    }

    render() {
        const {
            editable,
            info,
            isDirty,
            isEditMode,
            isLeafList,
            isLoading,
            isOperdata,
            isActionInput,
            kindpath,
            link,
            name,
            offset,
            path,
            rows,
            showEmpty,
            totalNumberOfResults,
        } = this.props;

        if (!isActionInput && !showEmpty && totalNumberOfResults < 1) {
            return null;
        }
        const { showAddListItemModal, errorOnCreate } = this.state;

        const chunkSize = this.getChunkSize();
        const columns = this.getColumns();
        const buttons = this.getButtons(columns);
        const empty = !(rows && rows.length > 0);
        const additionalElements = [
            (empty && !isLoading) &&
            <div data-handle="emptyMessage" key="empty" style={style.list.emptyMessage} >
                This list is empty
            </div>,
            buttons,
            <Pagination
                key="pagination"
                totalNumberOfResults={totalNumberOfResults}
                onGoToPage={this.onGoToPage}
                chunkSize={chunkSize}
                offset={offset}
            />,
        ];
        const stl = style.list.container;
        return (
            <div style={!isLeafList ? { margin: '1rem 0', ...stl } : { ...stl, width: '100%' }}>
                <List
                    additionalElements={additionalElements}
                    chunkSize={chunkSize}
                    columns={columns.filter(c => c.isVisible)}
                    info={info}
                    isEditMode={isEditMode}
                    isLoading={isLoading}
                    isOperdata={isOperdata}
                    ListComponent={Link}
                    name={name}
                    nrOfStaticColumns={editable ? 2 : 0}
                    offset={offset}
                    pageLength={isLeafList ? LEAF_LIST_CHUNK_SIZE : LIST_CHUNK_SIZE}
                    rows={rows}
                    totalNumberOfResults={totalNumberOfResults}
                    link={link}
                />
                {(empty && isDirty) && <div data-handle="emptyDirty" style={style.list.emptyDirty} />}
                {showAddListItemModal && <ModalSchemaInputContainer
                    title={translate(Messages.LIST.ADD_NEW_ITEM_TITLE)}
                    kindpath={kindpath}
                    path={path}
                    closeOnConfirm={false}
                    onClose={this.onClose}
                    onConfirm={this.onConfirm}
                    error={errorOnCreate}
                />}
            </div>
        );
    }
}

ListContainer.propTypes = {
    applicationName: PropTypes.string.isRequired,
    chunkSize: PropTypes.number,
    columnChildren: PropTypes.arrayOf(PropTypes.shape({
        kind: PropTypes.string.isRequired,
        name: PropTypes.string.isRequired,
        isEmptyLeaf: PropTypes.boolean,
    })),
    columnsVisibility: PropTypes.shape(Object),
    editable: PropTypes.bool,
    info: PropTypes.arrayOf(PropTypes.object),
    isActionInput: PropTypes.bool,
    isDirty: PropTypes.bool,
    isEditMode: PropTypes.bool,
    isLeafList: PropTypes.bool,
    isLoading: PropTypes.bool,
    isModified: PropTypes.bool,
    isOperdata: PropTypes.bool,
    isSchemaLoaded: PropTypes.bool,
    kindpath: PropTypes.arrayOf(PropTypes.shape({
        kind: PropTypes.string.isRequired,
        name: PropTypes.string.isRequired,
        rootPath: PropTypes.string,
    })),
    link: PropTypes.string,
    name: PropTypes.string,
    offset: PropTypes.number,
    onCreate: PropTypes.func.isRequired,
    onError: PropTypes.func,
    onDeleteRows: PropTypes.func.isRequired,
    onQueryData: PropTypes.func.isRequired,
    onSetCheckBoxValues: PropTypes.func.isRequired,
    onSetColumnVisible: PropTypes.func.isRequired,
    path: PropTypes.string.isRequired,
    rows: PropTypes.arrayOf(Object),
    schema: PropTypes.shape(Object),
    showEmpty: PropTypes.bool,
    sortColumn: PropTypes.string,
    sortOrder: PropTypes.string,
    totalNumberOfResults: PropTypes.number,
};

ListContainer.defaultProps = {
    columnsVisibility: {},
    editable: true,
    isModified: false,
    idOperdata: false,
    offset: 1,
};

const mergeProps = (stateProps, dispatchProps, ownProps) => {
    const { dispatch } = dispatchProps;
    const mergedProps = { ...ownProps, ...stateProps };
    // ENG-20233 List should not query empty lists
    const onQueryData = (args) => {
        const {
            columnChildren,
            isLeafList,
            isSchemaLoaded,
            offset,
            path,
            sortColumn,
            sortOrder,
            chunkSize,
        } = { ...mergedProps, ...args };

        let selection;
        if (!isSchemaLoaded) {
            return Promise.resolve(undefined);
        }

        if (!isLeafList) {
            selection = getQuerySelection({ columnChildren });
        }
        return dispatch(nodesActions.query({
            selection,
            path,
            sort: sortColumn,
            sortOrder,
            initialOffset: offset,
            chunkSize,
        }));
    };

    return ({
        ...mergedProps,
        onCreate(createPath, chunkSize) {
            return dispatch(createDisallowExistsThunk({ path: createPath })).then((result) => {
                if (result && result.error) return result;
                return onQueryData({ chunkSize });
            });
        },
        onError(path, error) {
            return dispatch(nodesActions.createErrorAction(path, new Error(error)));
        },
        onDeleteRows(keyPaths, chunkSize) {
            return dispatch(deleteNodesThunk({ paths: keyPaths }))
                .then(() => onQueryData({ chunkSize }));
        },

        onSetCheckBoxValues(path, keyPaths, value) {
            return dispatch(nodesActions.setCheckBoxValues(path, keyPaths, value));
        },

        onSetColumnVisible(path, columnKey, value) {
            return dispatch(nodesActions.setColumnVisibilityAction(path, columnKey, value));
        },
        onQueryData,
    });
};

const makeMapStateToProps = () => {
    const rowsSelector = createNodeRowSelector();
    const columnsSelector = createColumnsVisibilitySelector();

    const mapStateToProps = (state, ownProps) => {
        const { path, info } = ownProps;
        const applicationName = selectApplicationName(state);
        const isEditMode = selectIsEditMode(state);
        const infoArray = isEditMode ? info : [];
        const name = lstrip(path.split('/').pop(), '/');
        const link = selectLocationPathnameFromApplicationName(
            applicationName,
            escapeKey(path),
        );

        // ENG-20233 List should not query empty lists
        const node = selectNode(state, path) || {};
        const isLoading = node.isLoading || selectConfigIsLoading(state, path);
        const isModified = node.isModified;
        return {
            applicationName,
            columnsVisibility: columnsSelector(state, ownProps),
            info: infoArray,
            isEditMode,
            isDirty: selectDirty(state, path),
            isLoading,
            isModified,
            link,
            name,
            offset: selectOffset(state, path),
            rows: rowsSelector(state, ownProps),
            showEmpty: selectShowEmpty(state),
            sortColumn: selectSortColumn(state, path),
            sortOrder: selectsortOrder(state, path),
            totalNumberOfResults: selectTotalNumberOfResults(state, path),
        };
    };
    return mapStateToProps;
};

export default connect(
    makeMapStateToProps,
    undefined,
    mergeProps,
)(ListContainer);

export {
    ListContainer as _ListContainer,
    buildColsDataFromSchemaChildren as _buildColsDataFromSchemaChildren,
    buildWidgetLeaf as _buildWidgetLeaf,
    createBaseColumns as _createBaseColumns,
    createColumn as _createColumn,
    createColumns as _createColumns,
    createEmptyCellFactory as _createEmptyCellFactory,
    createIsDirtyColumn as _createIsDirtyColumn,
    createIsSelectedColumn as _createIsSelectedColumn,
    makeSpaceSepparatedList as _makeSpaceSepparatedList,
    isLinkable as _isLinkable,
    makeMapStateToProps as _makeMapStateToProps,
    mergeProps as _mergeProps,
    validateForm as _validateForm,
};
