import {
    selectLocationPathnameFromPath,
    selectLocationPathnameFromApplicationName,
} from 'modules/routing/selectors';
import {
    selectLoading,
    selectRows,
} from 'modules/nodes/selectors';
import {
    quoteKey,
} from '@webui-one/core/functional_utils';
import { selectSchema } from 'modules/schemas/reducer';
import * as S from '@webui-one/core/schema';
import { CONFIGURATION_EDITOR_NAME } from 'ConfigurationEditor';

const ALL_STATUS = 'all';
const FAILED_STATUS = 'failed';
const REACHED_STATUS = 'reached';
const NOT_REACHED_STATUS = 'not-reached';
const UNKNOWN_STATUS = 'unknown';

export const getStatusFromStats = (statusStats) => {
    if (statusStats[FAILED_STATUS] > 0) {
        return FAILED_STATUS;
    }
    if (statusStats[ALL_STATUS] > 0 && statusStats[REACHED_STATUS] === statusStats[ALL_STATUS]) {
        return REACHED_STATUS;
    }
    return UNKNOWN_STATUS;
};

export const getStatusStats = arr => arr.reduce((acc, c) => {
    acc[c.status] = (acc[c.status] || 0) + 1;
    return acc;
}, { [FAILED_STATUS]: 0, [REACHED_STATUS]: 0, [NOT_REACHED_STATUS]: 0, [ALL_STATUS]: arr.length });


export const getPlanComponentPath = parentPath => `${parentPath}/component`;

export const getPlanComponentKeyPath = (parentPath, componentKeys) =>
    `${getPlanComponentPath(parentPath)}{${componentKeys.map(quoteKey).join(' ')}}`;

export const getPlanComponentStatePath = (parentPath, componentKey) =>
    `${getPlanComponentKeyPath(parentPath, componentKey)}/state`;

export const getPlanComponentStateKeyPath = (parentPath, componentKeys, stateKey) =>
    `${getPlanComponentStatePath(parentPath, componentKeys)}{${quoteKey(stateKey)}}`;

export const selectComponents = (state, path) => selectRows(state, getPlanComponentPath(path));

export const selectComponentStates = (state, path, componentKeys) =>
    selectRows(state, getPlanComponentStatePath(path, componentKeys));

export const selectPlanLoading = (state, path) => selectLoading(state, getPlanComponentPath(path));

export const getComponentKeys = (state, path) => {
    const schema = selectSchema(state, path);
    const keys = S.getKeys(schema);
    return keys;
};

export const selectPlanComponents = (state, planPath) => {
    const componentKeys = getComponentKeys(state, getPlanComponentPath(planPath));
    return selectComponents(state, planPath).map((c) => {
        const name = c.name;
        const keys = componentKeys.map(ck => c[ck]);
        const rows = selectComponentStates(state, planPath, keys);
        const states = rows.map(s => ({
            ...s,
            to: selectLocationPathnameFromApplicationName(
                CONFIGURATION_EDITOR_NAME,
                getPlanComponentStateKeyPath(planPath, keys, s.name),
            ),
        }));
        const statusStats = getStatusStats(states);
        const status = getStatusFromStats(statusStats);
        const to = selectLocationPathnameFromPath(
            state,
            getPlanComponentKeyPath(planPath, keys),
        );
        return { name, states, statusStats, status, to };
    });
};
export const selectPlan = (state, path) => {
    const components = selectPlanComponents(state, path);
    const statusStats = getStatusStats(components);
    const status = getStatusFromStats(statusStats);
    return { components, statusStats, status };
};
