import React from 'react';
import PropTypes from 'prop-types';
import style from './InputStyles';

class ReadOnlyInput extends React.Component {
    constructor(props) {
        super(props);
        this.inputStyle = this.inputStyle.bind(this);
    }

    inputStyle() {
        const { isOperdata } = this.props;
        return isOperdata ? style.inputFieldReadOnlyOperdata : style.inputFieldReadOnly;
    }

    render() {
        const {
            name,
            value,
            isDefaultValue,
        } = this.props;
        const text = isDefaultValue ? `(${value})` : value;
        const inputStyle = this.inputStyle();

        return (
            <div data-handle={name} style={inputStyle}>
                {text}
            </div>
        );
    }
}

ReadOnlyInput.propTypes = {
    isDefaultValue: PropTypes.bool,
    isOperdata: PropTypes.bool,
    name: PropTypes.string,
    value: PropTypes.string,
};

ReadOnlyInput.defaultProps = {
    value: '',
};

export default ReadOnlyInput;
