import createFsm from 'core/fsm';

const START = Symbol('START');
const YEAR_1D = Symbol('YEAR_1D');
const YEAR_2D = Symbol('YEAR_2D');
const YEAR_3D = Symbol('YEAR_3D');
const YEAR_4D = Symbol('YEAR_4D');
const YEAR_MONTH_SEP = Symbol('YEAR_MONTH_SEP');
const MONTH_1D = Symbol('MONTH_1D');
const MONTH_2D = Symbol('MONTH_2D');
const MONTH_DAY_SEP = Symbol('MONTH_DAY_SEP');
const DAY_1D = Symbol('DAY_1D');
const DAY_2D = Symbol('DAY_2D');
const DAY_HOUR_SEP = Symbol('DAY_HOUR_SEP');
const HOUR_1D = Symbol('HOUR_1D');
const HOUR_2D = Symbol('HOUR_2D');
const HOUR_MIN_SEP = Symbol('HOUR_MIN_SEP');
const MIN_1D = Symbol('MIN_1D');
const MIN_2D = Symbol('MIN_2D');
const ERROR = Symbol('ERROR');
const MIN_SEC_SEP = Symbol('MIN_SEC_SEP');
const SEC_1D = Symbol('SEC_1D');
const SEC_2D = Symbol('SEC_2D');
const ZERO_TZ = Symbol('ZERO_TZ');
const PLUS_TZ_SEP = Symbol('PLUS_TZ_SEP');
const MINUS_TZ_SEP = Symbol('MINUS_TZ_SEP');
const TZ_HOUR_1D = Symbol('TZ_HOUR_1D');
const TZ_HOUR_2D = Symbol('TZ_HOUR_2D');
const TZ_HOUR_MIN_SEP = Symbol('TZ_HOUR_MIN_SEP');
const TZ_MIN_1D = Symbol('TZ_MIN_1D');
const TZ_MIN_2D = Symbol('TZ_MIN_2D');

export const stateSymbols = {
    START,
    YEAR_1D,
    YEAR_2D,
    YEAR_3D,
    YEAR_4D,
    YEAR_MONTH_SEP,
    MONTH_1D,
    MONTH_2D,
    MONTH_DAY_SEP,
    DAY_1D,
    DAY_2D,
    DAY_HOUR_SEP,
    HOUR_1D,
    HOUR_2D,
    HOUR_MIN_SEP,
    MIN_1D,
    MIN_2D,
    ERROR,
    MIN_SEC_SEP,
    SEC_1D,
    SEC_2D,
    ZERO_TZ,
    PLUS_TZ_SEP,
    MINUS_TZ_SEP,
    TZ_HOUR_1D,
    TZ_HOUR_2D,
    TZ_HOUR_MIN_SEP,
    TZ_MIN_1D,
    TZ_MIN_2D,
};

const getSeparatorChar = (state) => {
    switch(state) {
    case YEAR_4D:
    case MONTH_2D:
        return '-';
    case DAY_2D:
        return 'T';
    case HOUR_2D:
    case MIN_2D:
        return ':';
    default:
        return undefined;
    }
};

const createState = (next, value) => {
    if (next === ERROR) {
        return { next, value: { value, error: true } };
    }
    return { next, value: { ...value, state: next, separator: getSeparatorChar(next) } };
};

const isDigit = v => /^\d$/.test(v);
const isZero = v => v === '0';
const isOne = v => v === '1';
const isTwo = v => v === '2';
const isThree = v => v === '3';
const isDash = v => v === '-';
const isColon = v => v === ':';
const isT = v => v === 'T';
const isZ = v => v === 'Z';
const isPlus = v => v === '+';
const isMinus = v => v === '-';

const testChar = (re, v) => new RegExp(`^${re}$`).test(v);

const createDateFsmDefinition = () => ({
    [START]: (value) => {
        if (isDigit(value)) return createState(YEAR_1D, { value });
        return createState(ERROR, value);
    },
    [YEAR_1D]: (value) => {
        if (isDigit(value)) return createState(YEAR_2D, { value });
        return createState(ERROR, value);
    },
    [YEAR_2D]: (value) => {
        if (isDigit(value)) return createState(YEAR_3D, { value });
        return createState(ERROR, value);
    },
    [YEAR_3D]: (value) => {
        if (isDigit(value)) return createState(YEAR_4D, { value });
        return createState(ERROR, value);
    },
    [YEAR_4D]: (value) => {
        if (isDash(value)) return createState(YEAR_MONTH_SEP, { value });
        return createState(ERROR, value);
    },
    [YEAR_MONTH_SEP]: (value) => {
        if (isDash(value)) {
            return createState(YEAR_MONTH_SEP, { value, duplicateSeparator: true });
        }

        if (testChar('[01]', value)) return createState(MONTH_1D, { value });
        return createState(ERROR, value);
    },
    [MONTH_1D]: (value, { previousResult }) => {
        const previousValue = previousResult.value.value;

        if (isZero(previousValue) && isZero(value)) return createState(ERROR, value);
        if (isOne(previousValue) && !testChar('[012]', value)) {
            return createState(ERROR, value);
        }

        if (isDigit(value)) {
            return createState(MONTH_2D, { value });
        }
        return createState(ERROR, value);
    },
    [MONTH_2D]: (value) => {
        if (isDash(value)) return createState(MONTH_DAY_SEP, { value });
        return createState(ERROR, value);
    },
    [MONTH_DAY_SEP]: (value) => {
        if (isDash(value)) {
            return createState(MONTH_DAY_SEP, { value, duplicateSeparator: true });
        }
        if (testChar('[0123]', value)) return createState(DAY_1D, { value });
        return createState(ERROR, value);
    },
    [DAY_1D]: (value, { previousResult }) => {
        const previousValue = previousResult.value.value;
        if (isZero(previousValue) && isZero(value)) return createState(ERROR, value);
        if (isThree(previousValue) && !testChar('[01]', value)) {
            return createState(ERROR, value);
        }
        if (isDigit(value)) return createState(DAY_2D, { value });
        return createState(ERROR, value);
    },
    [DAY_2D]: (value) => {
        if (isT(value)) return createState(DAY_HOUR_SEP, { value });
        return createState(ERROR, value);
    },
    [DAY_HOUR_SEP]: (value) => {
        if (isT(value)) {
            return createState(DAY_HOUR_SEP, { value, duplicateSeparator: true });
        }
        if (testChar('[012]', value)) return createState(HOUR_1D, { value });
        return createState(ERROR, value);
    },
    [HOUR_1D]: (value, { previousResult }) => {
        const previousValue = previousResult.value.value;
        if (isTwo(previousValue) && !testChar('[0123]', value)) {
            return createState(ERROR, value);
        }
        if (isDigit(value)) return createState(HOUR_2D, { value });
        return createState(ERROR, value);
    },
    [HOUR_2D]: (value) => {
        if (isColon(value)) return createState(HOUR_MIN_SEP, { value });
        return createState(ERROR, value);
    },
    [HOUR_MIN_SEP]: (value) => {
        if (isColon(value)) {
            return createState(HOUR_MIN_SEP, { value, duplicateSeparator: true });
        }
        if (testChar('[012345]', value)) return createState(MIN_1D, { value });
        return createState(ERROR, value);
    },
    [MIN_1D]: (value) => {
        if (isDigit(value)) return createState(MIN_2D, { value });
        return createState(ERROR, value);
    },
    [MIN_2D]: (value) => {
        if (isColon(value)) return createState(MIN_SEC_SEP, { value });
        return createState(ERROR, value);
    },
    [MIN_SEC_SEP]: (value) => {
        if (isColon(value)) {
            return createState(MIN_SEC_SEP, { value, duplicateSeparator: true });
        }
        if (testChar('[012345]', value)) return createState(SEC_1D, { value });
        return createState(ERROR, value);
    },
    [SEC_1D]: (value) => {
        if (isDigit(value)) return createState(SEC_2D, { value });
        return createState(ERROR, value);
    },
    [SEC_2D]: (value) => {
        if (isZ(value)) return createState(ZERO_TZ, { value });
        if (isPlus(value)) return createState(PLUS_TZ_SEP, { value });
        if (isMinus(value)) return createState(MINUS_TZ_SEP, { value });
        return createState(ERROR, value);
    },
    [ZERO_TZ]: value => createState(ERROR, value),
    [PLUS_TZ_SEP]: (value) => {
        if (testChar('[01]', value)) return createState(TZ_HOUR_1D, { value });
        return createState(ERROR, value);
    },
    [MINUS_TZ_SEP]: (value) => {
        if (testChar('[01]', value)) return createState(TZ_HOUR_1D, { value });
        return createState(ERROR, value);
    },
    [TZ_HOUR_1D]: (value, { previousResult }) => {
        const previousValue = previousResult.value.value;
        if (isOne(previousValue) && !testChar('[01234]', value)) {
            return createState(ERROR, value);
        }
        if (isDigit(value)) return createState(TZ_HOUR_2D, { value });
        return createState(ERROR, value);
    },
    [TZ_HOUR_2D]: (value) => {
        if (isColon(value)) return createState(TZ_HOUR_MIN_SEP, { value });
        if (testChar('[012345]', value)) return createState(TZ_MIN_1D, { value });
        return createState(ERROR, value);
    },
    [TZ_HOUR_MIN_SEP]: (value) => {
        if (testChar('[012345]', value)) return createState(TZ_MIN_1D, { value });
        return createState(ERROR, value);
    },
    [TZ_MIN_1D]: (value) => {
        if (isDigit(value)) return createState(TZ_MIN_2D, { value });
        return createState(ERROR, value);
    },
    [TZ_MIN_2D]: value => createState(ERROR, value),
    [ERROR]: () => ({ next: ERROR }),
});

export const createDateFsm = () => {
    const definition = createDateFsmDefinition();
    return createFsm(START, definition);
};

export const parse = (dateTimeString) => {
    const fsm = createDateFsm();
    return [...dateTimeString].reduce((acc, cur) => {
        if (acc && acc.error) return acc;

        const result = fsm.next(cur);
        const currentValue = result.value.value;
        const parsedValue = acc.parsedValue ? `${acc.parsedValue}${currentValue}` : `${currentValue}`;
        return {
            ...result.value,
            parsedValue,
        };
    }, {});
};

export const autoComplete = (value) => {
    const parsed = parse(value);
    if (parsed.state === YEAR_MONTH_SEP) {
        return `${value}01-01T00:00:00`;
    }
    if (parsed.state === YEAR_4D) {
        return `${value}-01-01T00:00:00`;
    }
    if (parsed.state === MONTH_DAY_SEP) {
        return `${value}01T00:00:00`;
    }
    if (parsed.state === MONTH_2D) {
        return `${value}-01T00:00:00`;
    }
    if (parsed.state === DAY_2D) {
        return `${value}T00:00:00`;
    }
    if (parsed.state === HOUR_MIN_SEP) {
        return `${value}00:00`;
    }
    if (parsed.state === HOUR_2D) {
        return `${value}:00:00`;
    }
    if (parsed.state === MIN_SEC_SEP) {
        return `${value}00`;
    }
    if (parsed.state === MIN_2D) {
        return `${value}:00`;
    }
    if (parsed.state === MINUS_TZ_SEP || parsed.state === PLUS_TZ_SEP) {
        return `${value}00:00`;
    }
    if (parsed.state === TZ_HOUR_MIN_SEP) {
        return `${value}00`;
    }
    if (parsed.state === TZ_HOUR_2D) {
        return `${value}:00`;
    }
    return value;
};
