import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { toKeypath } from '@webui-one/core/kindpath';

import { getSchema as _getSchema } from 'modules/schemas/actions';
import { loadStatus, selectSchema as _selectSchema } from 'modules/schemas/reducer';

const isComplete = schema => schema && schema.loadStatus === loadStatus.COMPLETE;
const isNotComplete = schema => schema === undefined || schema.loadStatus !== loadStatus.COMPLETE;

class GetSchemaContainer extends React.Component {
    componentDidMount() {
        const { onGetSchema, schema } = this.props;
        if (schema === undefined) {
            return onGetSchema(false);
        }
        if (schema.loadStatus === loadStatus.PARTIAL) {
            return onGetSchema(true);
        }

        if (schema.loadStatus === loadStatus.SHOULD_REEVALUATE) {
            return onGetSchema(true);
        }
        return undefined;
    }

    shouldComponentUpdate(nextProps) {
        const { path, schema } = this.props;
        if (path !== nextProps.path) return true;

        if (isNotComplete(schema) && isComplete(nextProps.schema)) return true;

        return false;
    }

    componentDidUpdate(prevProps) {
        const { onGetSchema, path } = this.props;
        if (prevProps.path !== path) onGetSchema(path);
    }

    render() {
        const { children, schema } = this.props;
        if (schema === undefined) return null;
        if (schema.loadStatus !== loadStatus.COMPLETE) return null;

        return React.cloneElement(children, { schema });
    }
}

GetSchemaContainer.propTypes = {
    children: PropTypes.element,
    onGetSchema: PropTypes.func,
    path: PropTypes.string.isRequired,
    schema: PropTypes.shape(Object),
};

const getKeypathFromProps = ({ path, kindpath }) => {
    if (kindpath) return toKeypath(kindpath);
    return path;
};

const createMapStateToProps = ({ selectSchema = _selectSchema } = {}) => (state, ownProps) => {
    const path = getKeypathFromProps(ownProps);
    const schema = selectSchema(state, path);

    return {
        schema,
    };
};

const createMapDispatchToProps = ({ getSchema = _getSchema } = {}) => (dispatch, ownProps) => ({
    onGetSchema(reload) {
        const path = getKeypathFromProps(ownProps);
        return dispatch(getSchema({ path, reload }));
    },
});

export default connect(
    createMapStateToProps(),
    createMapDispatchToProps(),
)(GetSchemaContainer);

export {
    getKeypathFromProps as _getKeypathFromProps,
    GetSchemaContainer as _GetSchemaContainer,
    createMapStateToProps as _createMapStateToProps,
    createMapDispatchToProps as _createMapDispatchToProps,
};
